import { Container, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';

import ActivateProfile from '../../components/Activate/ActivateProfile';

import { getAddressForForm } from '../../http/index';

import MyLoader from '../../components/MyLoading/MyLoader';



export default function CustomerActivate() {

    const [addressData, setAddressData] = useState({});

    const [ld, setld] = useState(true);

    useEffect(() => {

        //console.log("useeffect calling for addresscity state");

        (async () => {

            try {

                const { data } = await getAddressForForm();

                //console.log(data);

                if (data.or) {
                    setAddressData(data.result);
                }

            } catch (error) {
                
                //console.log(error);

            }

            return setld(false);


        })();
    }, []);

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>
                        <Typography variant='h6' style={{ marginTop: "32px", marginBottom: "32px", textAlign: 'center', textTransform: 'capitalize' }}>
                            welcome to customer activated from name full name address contry email
                        </Typography>
                    </Container>
                    <ActivateProfile addressData={addressData}></ActivateProfile>
                </>
            }




        </div>
    )
}
