import { Button, Container, Grid, Typography, Box } from '@material-ui/core';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';

import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import { useHistory, useParams } from 'react-router-dom';

import { getSingleOrder, cancelCustomerOrder } from '../../http/index';
import MyLoader from '../../components/MyLoading/MyLoader';

export default function SingleOrderPage() {
    const [ld, setld] = useState(true);


    const [tr, setTr] = useState({ items: [], fullAddress: {}, pincodeId: {} });
    const { id } = useParams();
    const history = useHistory();

    async function cancelOrder(e) {

        e.preventDefault();

        // console.log("button clicked");

        //console.log(id);
        //orderId

        try {

            const { data } = await cancelCustomerOrder({ orderId: id });
            //console.log(data);

            if (data.or) {

                return setTr(data.result);
            }

        } catch (error) {

            //console.log(error);

        }


    }

    useEffect(() => {
        // console.log("use effect calling for single ");

        (async () => {

            try {
                const { data } = await getSingleOrder(id);

                //console.log(data);

                if (data.or) {
                    setTr(data.d);

                }
            } catch (error) {
                //console.log(error);

            }

            return setld(false);

        })()

    }, [id])

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>

                        <Typography variant='subtitle2' style={{ marginBottom: "16px", marginTop: "32px" }}>
                            Order Id {tr._id}
                        </Typography>
                        <Typography style={{ color: "green", textTransform: 'capitalize', marginBottom: "16px" }}>
                            order status {tr.status}
                        </Typography>
                    </Container>



                    {
                        tr.status === 'cancel' ? <>

                            <Container>
                                <Grid container justifyContent='center' style={{ marginTop: "32px", marginBottom: "32px" }}>
                                    <Grid item >
                                        <Button variant='contained' color='primary' onClick={() => {
                                            history.push('/');
                                        }}>
                                            Continue Shopping
                                        </Button>
                                    </Grid>

                                </Grid>
                            </Container>

                        </> : <>
                            <Container>

                                <Typography variant='h6' style={{ marginBottom: "8px", }}>
                                    Items  {tr.totalquantity}
                                </Typography>
                                {
                                    tr.items.map((i) => {
                                        return (
                                            <Grid key={i._id} container alignItems='center' style={{ textTransform: 'capitalize', marginBottom: "24px" }}>
                                                <Grid item xs={6} sm={2} container justifyContent='center' alignItems='center'>
                                                    <Grid item>
                                                        <img src={i.productImage} alt={i.name} style={{ maxWidth: "100%", height: "130px" }}>
                                                        </img>
                                                    </Grid>

                                                </Grid>

                                                <Grid item xs={6} sm>
                                                    <Typography variant='subtitle2' style={{ marginBottom: "8px", }}>
                                                        {i.name}
                                                    </Typography>
                                                    <Typography style={{ marginBottom: "8px", }}>
                                                        total quantity {i.quantity}
                                                    </Typography>
                                                    <Typography variant='subtitle2'>
                                                        total amount <i className="fa fa-inr" aria-hidden="true"></i>  {i.totalamount}
                                                    </Typography>
                                                </Grid>

                                            </Grid>)
                                    })
                                }

                            </Container>
                            <Container>

                                <Typography variant='subtitle2' style={{ marginBottom: "8px", }}>
                                    Order Summary
                                </Typography>
                                <Grid container alignItems='center' justifyContent='space-between' style={{ textTransform: 'capitalize' }}>
                                    <Grid item>
                                        <Typography style={{ marginBottom: "8px", }}>
                                            Total  quantity {tr.totalquantity}
                                        </Typography>
                                        <Typography style={{ marginBottom: "8px", }}>
                                            Total  amount <i className="fa fa-inr" aria-hidden="true"></i>  {tr.totalamount}
                                        </Typography>
                                        <Typography style={{ marginBottom: "8px", }}>
                                            total devlivery charge <i className="fa fa-inr" aria-hidden="true"></i>  {tr.totaldeliverycharge}
                                        </Typography>
                                        <Typography variant='h6' style={{ marginBottom: "8px", }}>
                                            total amount of Order <i className="fa fa-inr" aria-hidden="true"></i>  {tr.totalAmountofOrder}
                                        </Typography>
                                    </Grid>
                                    <Grid item>

                                        <Typography style={{ marginBottom: "8px", }}>
                                            createdAt {" "}
                                            {
                                                moment(tr.createdAt).format('LLL')
                                            }
                                        </Typography>
                                        <Typography variant='subtitle2' style={{ marginBottom: "8px", }}>
                                            deliverDate {" "}
                                            {
                                                moment(tr.deliverDate).format('LL')
                                            }
                                        </Typography>
                                        <Typography variant='subtitle2' style={{ marginBottom: "8px", }}>
                                            devlivery Address   {tr.address} {" "} {tr.fullAddress.nameOfCity} {" "} {tr.fullAddress.stateOfCity} {" "} {tr.fullAddress.countryOfCity}
                                            {" "} {tr.pincodeId.pincode}
                                        </Typography>
                                        
                                        <Typography variant='subtitle2' style={{ marginBottom: "8px", }}>
                                            payment type   {tr.paymentType}
                                        </Typography>

                                    </Grid>


                                </Grid>
                            </Container>

                        </>
                    }




                    <Container>



                        <Box marginTop={"16px"} marginBottom="16px">
                            {
                                tr.status === 'placed' ?
                                    <Accordion>
                                        <AccordionSummary
                                            expandIcon={<ExpandMoreIcon />}
                                            aria-controls="panel1a-content"
                                            id="panel1a-header"
                                        >
                                            <Typography variant='subtitle2'>
                                                Cancel your  Order
                                            </Typography>
                                        </AccordionSummary>
                                        <AccordionDetails>

                                            <Button onClick={cancelOrder} variant='contained' color='secondary' style={{ marginTop: "16px", marginBottom: "16px" }}>
                                                Cancel Order
                                            </Button>
                                        </AccordionDetails>
                                    </Accordion>
                                    : <>

                                    </>
                            }
                        </Box>
                    </Container>

                </>
            }



        </div>
    )
}


