import axios from 'axios';
import { useEffect, useState } from 'react';

import { useDispatch } from 'react-redux';

import { setAuth } from '../store/customerSlice';

import { setCart } from '../store/cartSlice';


export function useLoadingWithRefresh() {
    const [loading, setLoading] = useState(true);

    const dispatch = useDispatch();
    useEffect(() => {
        //console.log("refresh for customer useeffecting calling");

        // promise ka use kart hai 

        (async () => {

            try {

                const result = await refreshApi();

                //console.log(result);

                if (result.auth) {

                    dispatch(setAuth({ customer: result.customerDto }));

                }

                setLoading(false);

            } catch (error) {

                //console.log("error of api refresh");

                //console.log(error);
                // not set false

            }

            // other async wokr


            try {

                const rs = await usercart();

                //console.log(rs);

                if (rs.c) {

                    console.log(rs.cart.length);
                    dispatch(setCart(rs.cart.length));
                }

                setLoading(false);

            } catch (error) {

                //console.log(error);
                setLoading(false);


            }




        })();


        function refreshApi() {


            return new Promise(async (reslove, reject) => {

                try {
                    const { data } = await axios.get('https://storeclubs.com/api/customer/refresh', { withCredentials: true });

                    return reslove(data);

                } catch (error) {

                    return reject(error);


                }

            });


        }


        function usercart() {

            return new Promise(async (reslove, reject) => {

                try {
                    const { data } = await axios('https://storeclubs.com/api/customer/cus-cart/get-all', { withCredentials: true });


                    return reslove(data);

                } catch (error) {

                   // console.log("user cart error");

                    // ek aur request cookie ke liye

                    try {
                        const { data } = await axios.get('https://storeclubs.com/api/customer/cookie/get-cookie', { withCredentials: true });

                        return reslove(data);

                    } catch (error) {


                        return reject(error);

                    }


                }

            });

        }




    }, [dispatch]);


    return {
        loading
    }
}



