import { Container, Typography, Button, Grid, Paper } from '@material-ui/core';
import React, { useEffect, useState } from 'react'

import MyBtn from '../MyCom/MyBtn';
import { getSingleCartCustomer, getuserProfile, getDelivertimeforOrderSummary, registerOrder } from '../../http/index';

import moment from 'moment';

import { useHistory } from 'react-router-dom';

import MyLoader from '../MyLoading/MyLoader';

export default function StepOfOrderSummaryById({ id }) {

    // useeffect call karo findby id then

    const [ld, setld] = useState(true);

    const [user, setUser] = useState({ name: "", _id: "", phone: "", address1: "", fullAddress: {}, pincodeId: {} });

    const [delivery, setDelivery] = useState({ time: 0 });

    const [cr, setCr] = useState([]);

    const [orstatus, setorstatus] = useState(false);


    const history = useHistory();


    function goorder() {

        return history.push('/orders');

    }

    async function placeOrder() {

        //console.log("placed order button clicked");

        let ar = cr.map((i) => {

            return i.productId;
        });

        //console.log(ar);

        const mydata = {
            products: ar,
        };

        try {

            const { data } = await registerOrder(mydata);

            //console.log(data);

            if (data.or) {

                return setorstatus(true);

            }

        } catch (error) {

            //console.log(error);


        }


    }






    useEffect(() => {

        //console.log("useeffect calling single order age ");

        (async () => {

            try {


                const myuser = await getUserProfileInOrderSummary();

                //console.log(myuser);

                if (myuser.or) {
                    setUser(myuser.result);

                }

            } catch (error) {
                //console.log(error);

            }


            try {

                const mycart = await getSingleCartofCustomer(id);

                //console.log(mycart);

                if (mycart.or) {
                    setCr(mycart.result);
                }

            } catch (error) {

                // console.log(error);

            }

            try {

                const mydelivery = await getDeliverForOrderSummary();

                //console.log(mydelivery);

                if (mydelivery.or) {
                    setDelivery(mydelivery.result);

                }
            } catch (error) {

                //console.log(error);

            }


            return setld(false);

            function getSingleCartofCustomer(id) {

                return new Promise(async (resolve, reject) => {

                    try {
                        const { data } = await getSingleCartCustomer(id);

                        return resolve(data);
                    } catch (error) {

                        return reject(error);
                    }
                })
            }

            function getUserProfileInOrderSummary() {

                return new Promise(async (resolve, reject) => {

                    try {
                        const { data } = await getuserProfile();

                        return resolve(data);

                    } catch (error) {

                        return reject(error);

                    }
                })


            }



            function getDeliverForOrderSummary() {

                return new Promise(async (resolve, reject) => {

                    try {

                        const { data } = await getDelivertimeforOrderSummary();

                        return resolve(data);
                    } catch (error) {

                        return reject(error);
                    }
                });

            }



        })();
    }, [id]);

    return (
        <>
            {
                ld ? <MyLoader></MyLoader> : <>

                    <div style={{ marginTop: "24px" }}>


                        {

                            (cr.length === 0) ? <Container>
                                <MyBtn path={'/'} n='continue shopping'>
                                </MyBtn>
                            </Container> :
                                <div>

                                    {
                                        orstatus ? <>
                                            <Container>
                                                <Grid container justifyContent='center' alignItems='center'>

                                                    <Grid item >

                                                        <Typography style={{ color: "green", marginBottom: "24px" }}>
                                                            Order Placed Sucessfully Done
                                                        </Typography>

                                                        <Button color='primary' variant='contained' onClick={goorder}>
                                                            go to orders
                                                        </Button>

                                                    </Grid>

                                                </Grid>
                                            </Container>
                                        </> : <>
                                            <div>

                                                <Container >

                                                    <Grid container alignItems='center' style={{ textTransform: 'capitalize', marginBottom: "16px" }} >
                                                        <Grid item sm xs={12} style={{ marginBottom: "16px" }}>
                                                            <Typography variant='h6' style={{ marginBottom: "8px" }}>
                                                                {user.name}
                                                            </Typography>
                                                            <Typography >
                                                                phone number  {user.phone}
                                                            </Typography>
                                                        </Grid>
                                                        <Grid item xs={12} sm={3} >
                                                            <Typography variant="subtitle2" style={{ marginBottom: "8px" }}>
                                                                deliver to : {user.address1}
                                                            </Typography>
                                                            <Typography variant="subtitle2" style={{ marginBottom: "8px" }}>
                                                                {user.fullAddress.nameOfCity} {" "} {user.fullAddress.stateOfCity} {" "} {user.fullAddress.countryOfCity}  {" "}  {user.pincodeId.pincode}
                                                            </Typography>
                                                            <Typography variant="button" style={{ marginBottom: "8px" }}>
                                                                delivery date   {
                                                                    moment(Date.now() + parseInt(delivery.time)).format('LLL')
                                                                }
                                                            </Typography>
                                                        </Grid>
                                                    </Grid>


                                                </Container>

                                                <Container>

                                                    {
                                                        cr.map((i) => {

                                                            return (
                                                                <Paper key={i._id} style={{ marginBottom: "24px", padding: "16px 4px", textTransform: 'capitalize' }}>

                                                                    <Grid container spacing={1} alignItems='center'>

                                                                        <Grid item container sm={2} xs={3} justifyContent='center' alignItems='center'>
                                                                            <Grid item >
                                                                                <img src={i.productImage} alt={i.name} style={{ maxWidth: "100%", height: "100px" }}>
                                                                                </img>
                                                                            </Grid>

                                                                        </Grid>
                                                                        <Grid item xs>
                                                                            <Typography variant='subtitle2'>
                                                                                {i.name}
                                                                            </Typography>
                                                                            <Typography>
                                                                                total quantity {i.quantity}
                                                                            </Typography>
                                                                            <Typography>
                                                                                total amount <i className="fa fa-inr" aria-hidden="true"></i>  {i.totalamount}
                                                                            </Typography>
                                                                            <Typography>
                                                                                toatal devlivery <i className="fa fa-inr" aria-hidden="true"></i>  {i.totaldelivery}
                                                                            </Typography>
                                                                        </Grid>
                                                                    </Grid>


                                                                </Paper>
                                                            )
                                                        })
                                                    }


                                                </Container>


                                                <Container style={{ textTransform: 'capitalize' }}>
                                                    <Typography variant='h6' style={{ marginBottom: "16px" }}>
                                                        OrderSummary
                                                    </Typography>
                                                    <Typography style={{ marginBottom: "8px" }}>
                                                        total quantity   {cr.reduce((acc, i) => {
                                                            return acc + i.quantity;
                                                        }, 0)

                                                        }
                                                    </Typography>
                                                    <Typography style={{ marginBottom: "8px" }}>
                                                        totoal amount <i className="fa fa-inr" aria-hidden="true"></i>    {
                                                            cr.reduce((acc, i) => {
                                                                return acc + i.totalamount
                                                            }, 0)
                                                        }

                                                    </Typography>
                                                    <Typography style={{ marginBottom: "8px" }}>
                                                        total delivey cost <i className="fa fa-inr" aria-hidden="true"></i>   {
                                                            cr.reduce((acc, i) => {
                                                                return acc + i.totaldelivery
                                                            }, 0)
                                                        }
                                                    </Typography>
                                                    <Typography variant='h6' style={{ marginBottom: "8px" }}>
                                                        Total amount of Order <i className="fa fa-inr" aria-hidden="true"></i>  {
                                                            cr.reduce((acc, i) => {
                                                                return acc + i.totaldelivery + i.totalamount;
                                                            }, 0)
                                                        }
                                                    </Typography>


                                                    <Button onClick={placeOrder} variant='contained' color='primary' style={{ marginTop: "16px", marginBottom: "16px" }}>
                                                        placed order
                                                    </Button>
                                                </Container>

                                            </div>
                                        </>
                                    }

                                </div>
                        }

                    </div>
                </>
            }
        </>

    )
}
