
import React, { useState } from 'react';

import { activateProfile } from '../../http/index';

import { setAuth } from '../../store/customerSlice';

import { useDispatch } from 'react-redux';
import { Button, Box, Container, Grid, TextField, Typography, MenuItem } from '@material-ui/core';

export default function ActivateProfile({ addressData }) {

    //set {city:[],state:[],pin:[],country:[]}

    let { city, state, pincode, country } = addressData;


    const dispatch = useDispatch();
    let [name, setName] = useState('');

    let [address1, setAddress1] = useState('');
    let [address2, setAdress2] = useState('');

    let [pin, setPincode] = useState('');//pincode[0]

    let [ds, setDs] = useState('');//city[0]

    let [mys, setMs] = useState('');//state[0]

    const [er, setEr] = useState(false);
    async function submit(e) {

        e.preventDefault();

        //console.log("submit button clicked");

        
        if (!ds || !mys || !pin) {

            setEr(true);
            return;
        }
        
      
   
        if (!name || !address1 || !address2) {

            setEr(true);
            return;
        }
        const mydata = {
            name: name.toLowerCase(),
            address1: address1.toLowerCase(),
            address2: address2.toLowerCase(),
            city: ds,
            state: mys,
            country: country,
            pincode: parseInt(pin),
        };

        try {
            const { data } = await activateProfile(mydata);

           // console.log(data);
            //update the redux

            if (data.auth) {
                dispatch(setAuth({ customer: data.customerDto }));
                //onNext step step
            }

        } catch (error) {

            setEr(true);
           // console.log(error);

        }
    }

    return (
        <div>


            <Container>
                <form>

                    <Grid container justifyContent='center' style={{ textTransform: 'capitalize' ,marginTop:"32px"}}>
                        <Grid item xs={12} sm={5} >
                            {
                                er && <Box style={{ marginBottom: "24px" }}>
                                    <Typography style={{ color: 'red', textAlign: 'center' }}>
                                        All feilds Are Required
                                    </Typography>
                                </Box>
                            }
                            <Grid container spacing={2} alignItems='center' style={{ marginBottom: "24px" }}>
                                <Grid item >

                                    <Typography>
                                        Enter name
                                    </Typography>
                                </Grid>
                                <Grid item xs>
                                    <TextField type="text" style={{ width: "100%" }} variant='outlined' size='small' value={name} onChange={(e) => {
                                        setName(e.target.value);

                                    }}>

                                    </TextField>
                                </Grid>
                            </Grid>
                            <Grid container spacing={2} alignItems='center' style={{ marginBottom: "24px" }}>
                                <Grid item >

                                    <Typography>
                                        Enter first address
                                    </Typography>
                                </Grid>
                                <Grid item xs >
                                    <TextField type="text" style={{ width: "100%" }} variant='outlined' size='small' value={address1} onChange={(e) => {
                                        setAddress1(e.target.value);
                                    }}>

                                    </TextField>
                                </Grid>
                            </Grid>
                            <Grid container spacing={2} alignItems='center' style={{ marginBottom: "24px" }}>
                                <Grid item >

                                    <Typography>
                                        Enter second address
                                    </Typography>
                                </Grid>
                                <Grid item xs >
                                    <TextField type="text" style={{ width: "100%" }} variant='outlined' size='small' value={address2} onChange={(e) => {
                                        setAdress2(e.target.value);
                                    }}>

                                    </TextField>
                                </Grid>
                            </Grid>

                            <Grid container spacing={2} alignItems='center' style={{ marginBottom: "24px" }}>
                                <Grid item >
                                    <Typography>
                                        select city
                                    </Typography>
                                </Grid>
                                <Grid item xs>
                                    <TextField select style={{ width: "100%" }} variant='outlined' size='small' value={ds}  onChange={(e) => {
                                        setDs(e.target.value);
                                    }} >
                                        {
                                            city.map((i) => {
                                                return (<MenuItem value={i} key={i}>
                                                    {i}
                                                </MenuItem>)
                                            })
                                        }

                                    </TextField>
                                </Grid>
                            </Grid>

                            <Grid container spacing={2} alignItems='center' style={{ marginBottom: "24px" }}>
                                <Grid item >

                                    <Typography>
                                        select state
                                    </Typography>
                                </Grid>
                                <Grid item xs>
                                    <TextField select style={{ width: "100%" }} variant='outlined' size='small' value={mys} onChange={(e) => {
                                        setMs(e.target.value);
                                    }} >

                                        {
                                            state.map((i) => {
                                                return (<MenuItem value={i} key={i}>
                                                    {i}
                                                </MenuItem>)
                                            })
                                        }
                                    </TextField>
                                </Grid>
                            </Grid>

                            <Box style={{ marginBottom: "24px" }}>
                                <Typography>
                                    country {country}
                                </Typography>
                            </Box>

                            <Grid container spacing={2} alignItems='center' style={{ marginBottom: "24px" }}>
                                <Grid item >

                                    <Typography>
                                        select pincode of city
                                    </Typography>
                                </Grid>
                                <Grid item xs >
                                    <TextField select style={{ width: "100%" }} variant='outlined' size='small' value={pin} onChange={(e) => {
                                        setPincode(e.target.value);
                                    }} >

                                        {
                                            pincode.map((i) => {
                                                return (<MenuItem value={i} key={i}>
                                                    {i}
                                                </MenuItem>)
                                            })
                                        }
                                    </TextField>
                                </Grid>
                            </Grid>

                            <Box>
                                <Button onClick={submit} variant='contained' color='primary'>
                                    Submit
                                </Button>
                            </Box>
                        </Grid>
                    </Grid>
                    <Box>

                    </Box>

                </form>
            </Container>


        </div>
    )
}
