
import { Button, Container, Box, TextField, Typography, Grid } from '@material-ui/core';
import React, { useState } from 'react';

import { useDispatch } from 'react-redux';

import { sendOtp } from '../../http/index';

import { setOtp } from '../../store/customerSlice';


export default function SetpPhone({ onNext }) {

    const [phone, setphone] = useState('');

    const disptach = useDispatch();

    const [er, setEr] = useState(false);

    async function submit(e) {

        e.preventDefault();

        //console.log("button clicked ");

        // valdition
        if (!phone) {
            return setEr(true);
        }



        if (phone.length < 10) {
            return setEr(true);
        }

        try {
            const { data } = await sendOtp({ phone: `${phone}` });

         //console.log(data);

            disptach(setOtp({ phone: data.phone, hash: data.hash }));

            onNext();

        } catch (error) {

         //console.log("error in the sending phone number");

            setEr(true);
            
          //console.log(error);

        }


    }


    return (
        <div>

            <Container>
                <form>

                    <Grid container alignItems='center' justifyContent='center' >

                        <Grid item >
                            <Box marginBottom={"32px"}>
                                <Typography>
                                    Enter Phone Number
                                </Typography>

                            </Box>
                            {
                                er && <Box marginBottom={"32px"}>
                                    <Typography style={{ textTransform: 'capitalize', color: 'red' }}>
                                        Invalid Phone Number
                                    </Typography>

                                </Box>
                            }

                            <Box marginBottom={"32px"}>
                                <TextField type={'text'} variant='outlined' size="small" value={phone} onChange={(e) => {
                                    setphone(e.target.value);

                                }} ></TextField>
                            </Box>

                            <Box>
                                <Button onClick={submit} variant='contained' color='primary'>
                                    Login
                                </Button>
                            </Box>



                        </Grid>
                    </Grid>

                </form>
            </Container>

        </div>
    )
}




