import React from 'react';


import MyNavComp from './components/Navigation/MyNavComp';

import { Route, Switch, BrowserRouter, Redirect } from 'react-router-dom'

import HomePage from './pages/Home/HomePage';

import LoginPage from './pages/Login/LoginPage';


import CustomerCart from './pages/CustomerCart/CustomerCartPage';

import DecideCheckout from './pages/DecideCheckout/DecideCheckoutPage';

import Orders from './pages/Orders/OrdersPage';

import SingleOrder from './pages/SingleOrder/SingleOrderPage';

import Profile from './pages/Profile/ProfilePage';

import CookieCart from './pages/CookieCart/CookieCart';

import CustomerActivate from './pages/CustomerActivate/CustomerActivate';



import { useSelector } from 'react-redux';

import { useLoadingWithRefresh } from './hooks/useLoadingWithRefresh';

import NormalLogin from './pages/NormalLogin/NormalLogin';


import ResultsofSearch from './pages/ResultofSearch/ResultsofSearch';

import ProductPage from './pages/ProductPage/ProductPage';

import { useInitialCookie } from './hooks/useInitialCookie';

import EditProfile from './pages/EditProfile/EditProfile';


import MyLoading from "./components/MyLoading/MyLoading";

import AllSubCatergory from './pages/Shop/AllSubCatergory';
import AllSubProducts from './pages/Shop/AllSubProducts';

import AllService from './pages/MyService/AllService';

import BookService from './pages/MyService/BookService';
import CustomerService from './pages/MyService/CustomerService';
import SingleCustomerService from './pages/MyService/SingleCustomerService';

import AllShopByCatergory from './pages/Shop/AllCatergory';

import About from './pages/Shop/About';

import Contact from './pages/Shop/Contact';

import TermCond from './pages/Shop/TermsCond';

import ReturnPol from './pages/Shop/ReturnPolicy';


export default function App() {

  useInitialCookie();

  const { loading } = useLoadingWithRefresh();

  const { isAuth, customer } = useSelector((state) => state.auth);

  return (
   
   <>
    {
        loading ? <MyLoading></MyLoading> : <BrowserRouter>

          <MyNavComp></MyNavComp>
          <Switch>
            <Route path='/' exact>
              <HomePage></HomePage>
            </Route>


            <Route path='/search' exact>

              <ResultsofSearch></ResultsofSearch>
            </Route>

            <Route path={'/allcatergory'} exact>
              <AllShopByCatergory></AllShopByCatergory>
            </Route>

            <Route path={'/about'}>
              <About></About>
            </Route>

            <Route path={'/contact'}>
              <Contact></Contact>
            </Route>
            <Route path={'/return-policy'}>
              <ReturnPol></ReturnPol>
            </Route>
            <Route path={'/privacy-policy'}>
              <TermCond></TermCond>
            </Route>

            <Route path='/catergory/:id' exact>
              <AllSubCatergory></AllSubCatergory>
            </Route>

            <Route path='/subcatergory/:id' exact>

              <AllSubProducts></AllSubProducts>
            </Route>

            <Route path='/login' exact>

              {isAuth ? <Redirect to='/'></Redirect> : <LoginPage></LoginPage>}
            </Route>



            <Route path='/viewcart' exact>
              {
                isAuth ? <CustomerCart></CustomerCart> : <CookieCart></CookieCart>
              }

            </Route>

            <Route path='/viewcheckout' exact>
              {
                isAuth ? <DecideCheckout></DecideCheckout> : <NormalLogin></NormalLogin>
              }

            </Route>

            <Route path='/orders' exact>
              {
                isAuth ? <Orders></Orders> : <Redirect to='/login'></Redirect>
              }

            </Route>

            <Route path='/orders/:id' exact>
              {
                isAuth ? <SingleOrder></SingleOrder> : <Redirect to='/login'></Redirect>
              }
            </Route>
            <Route path='/profile' exact>

              {
                isAuth ? customer.activated ? <Profile></Profile> : <CustomerActivate></CustomerActivate> : <LoginPage></LoginPage>
              }
            </Route>

            <Route path='/profile/edit' exact>

              {
                isAuth ? customer.activated ? <EditProfile></EditProfile> : <CustomerActivate></CustomerActivate> : <LoginPage></LoginPage>
              }
            </Route>


            <Route path={'/all/services'} exact>
              <AllService></AllService>
            </Route>

            <Route path={'/bookservice'} exact>
              {
                isAuth ? customer.activated ? <BookService></BookService> : <CustomerActivate></CustomerActivate> : <NormalLogin></NormalLogin>
              }

            </Route>

            <Route path={'/myservices'} exact>
              {
                isAuth ? <CustomerService></CustomerService> : <Redirect to={'/login'}></Redirect>
              }

            </Route>

            <Route path={'/myservices/:id'} exact>
              {
                isAuth ? <SingleCustomerService></SingleCustomerService> : <Redirect to={'/login'}></Redirect>
              }

            </Route>

            <Route path='/pr/:id' exact>
              <ProductPage></ProductPage>
            </Route>
          </Switch>
        </BrowserRouter>


      }
   </>
  )
}
