import React, { useEffect, useState } from 'react';

import { getAllOrderOfCustomer } from '../../http/index';

import moment from 'moment';
import { Link } from 'react-router-dom';
import { Container, Grid, Paper, Typography } from '@material-ui/core';
import MyLoader from '../../components/MyLoading/MyLoader';

export default function OrdersPage() {

    const [ld, setld] = useState(true);

    const [tr, setTr] = useState([]);

    useEffect(() => {

        (async () => {

            try {
                const { data } = await getAllOrderOfCustomer();

                //console.log(data);

                if (data.or) {

                    setTr(data.result);

                }

            } catch (error) {
                //console.log(error);

            }

            return setld(false);
        })()

    }, []);

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>

                    <Container>
                        <Typography variant='h6' style={{ textAlign: 'center', marginBottom: "32px", marginTop: "32px", textTransform: 'capitalize' }}>

                            welcome to orders page
                        </Typography>
                    </Container>

                    <Container>
                        {
                            tr.map((i) => {

                                return (<Paper key={i._id} style={{ padding: "12px 12px", marginBottom: "24px" }}>


                                    <Link to={`/orders/${i._id}`} style={{ textDecoration: 'none', textTransform: 'capitalize' }}>
                                        orderId   {i._id}
                                    </Link>
                                    <Grid container spacing={3} alignItems='center' justifyContent='space-between' style={{ textTransform: 'capitalize', marginTop: "8px" }}>

                                        <Grid item xs={12} sm={8}>

                                            <Typography variant='h6' style={{ marginBottom: "8px" }}>
                                                total amount of order <i className="fa fa-inr" aria-hidden="true"></i>  {i.totalAmountofOrder}
                                            </Typography>

                                            <Typography variant='subtitle2' style={{ color: "green", marginBottom: "8px" }} >
                                                Order  status {i.status}
                                            </Typography>
                                            <Typography variant='subtitle2' style={{ marginBottom: "8px", }}>
                                                payment type   {i.paymentType}

                                            </Typography>

                                        </Grid>

                                        <Grid item xs={12} sm={4}>
                                            <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                                                createdAt {moment(i.createdAt).format('LLL')}
                                            </Typography>
                                            <Typography variant='subtitle2'>
                                                deliver date {moment(i.deliverDate).format('LL')}
                                            </Typography>
                                        </Grid>

                                    </Grid>


                                </Paper>)
                            })
                        }
                    </Container>

                </>
            }



        </div>
    )
}
