import React, { useEffect, useState } from 'react'

import { useParams, useHistory } from 'react-router-dom';

import { singleProduct } from '../../http/index';

import { addToCartCookie, addItemToCustomerCart, checkItemInCartCookie, checkItemInCustomerCart } from '../../http/index';

import { setBuyNow } from '../../store/buyNowSlice';

import moment from 'moment';

import { useSelector, useDispatch } from 'react-redux';
import { Box, Button, Container, Grid, makeStyles, Table, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';

import ProductSilder from './ProductSlider';


import MyLoader from '../../components/MyLoading/MyLoader';

const useStyles = makeStyles((theme) => ({

    first: {
        minHeight: "85vh",
        [theme.breakpoints.down('sm')]: {
            minHeight: "65vh",
        },


    },
    f1: {
        [theme.breakpoints.down('sm')]: {
            marginBottom: "0px",
        },
    },
    f2: {
        [theme.breakpoints.down('sm')]: {

        },
    },
    f3: {
        [theme.breakpoints.down('sm')]: {
            fontSize: "16px"
        },
    },
    f4: {
        [theme.breakpoints.down('sm')]: {
            fontSize: "14px"
        },
    },
    f5: {
        [theme.breakpoints.down('sm')]: {
            marginBottom: "24px",
        },
    },


}));
export default function ProductPage() {
    const classes = useStyles();
    const history = useHistory();

    const [ld, setld] = useState(true);


    const { isAuth } = useSelector((state) => state.auth);

    const disptach = useDispatch();

    //console.log("single product page ")

    const { id } = useParams();

    const [pr, setPr] = useState({
        prImages: [],
        deliverTimeId: { time: 0 },
        productMap: [{ mykey: {}, myvalue: {} }],
        aboutItem: [],

    });



    const [cokadd, setCokAdd] = useState(false);

    const [userCart, setUserCart] = useState(false);



    async function addtocartCookie() {

        //console.log("addto cart button cliecked for cookie");
        // console.log(pr._id);
        // request add to cart 
        try {
            const { data } = await addToCartCookie({ productId: pr._id });

            // console.log(data);

            if (data.or) {
                return setCokAdd(true);
            }

        } catch (error) {

            // console.log(error);

        }


    }


    async function addtocartUser() {

        //console.log("add to cart user cart button clicked");

        //console.log(pr._id);
        // productId:pr._id

        try {
            const { data } = await addItemToCustomerCart({ productId: pr._id });

            // console.log(data);

            if (data.or) {
                return setUserCart(true);
            }
        } catch (error) {

            // console.log(error);

        }



    }


    async function buyNowToCookie() {

        // history.push(`/viewcheckout?pr=${pr._id}`);
        // console.log("buynow button clicked to cookie redux help");
        //console.log(pr._id);

        //redux ka ismala karna hai
        disptach(setBuyNow(pr._id));//setBuyNow redux {pr:true,productid:""}

        return history.push(`/viewcheckout?pr=${pr._id}`);

    }


    async function buyNowToCart() {

        // history.push(`/viewcheckout?pr=${pr._id}`);
        //console.log("buynow button clicked to user cart");
        //console.log(pr._id);

        try {

            const { data } = await addItemToCustomerCart({ productId: pr._id });

            //console.log(data);

            if (data.or) {

                //console.log("succefuly add to customer cart ");

                return history.push(`/viewcheckout?pr=${pr._id}`);

            }

        } catch (error) {

            //console.log(error);

        }



    }

    function goToCart() {

        history.push('/viewcart');
    }

    useEffect(() => {

        //console.log("useffect calling for single product");

        (async () => {

            try {
                const data = await getSingleByProductId(id);

                //console.log(data);


                if (data.or) {

                    setPr(data.result);

                }

            } catch (error) {

                //console.log(error);

            }

            //then 

            if (isAuth) {

                //console.log("user is login call check in the usert cart product id ");

                try {

                    const cusCartRes = await checkItemInCustomerCartProductId(id);

                    //console.log(cusCartRes);

                    if (cusCartRes.or) {
                        setUserCart(true);
                    }

                } catch (error) {

                    //console.log(error);

                }


            } else {

                //console.log("user is not login call cookies check cart is present ");

                try {

                    const cookieCartRes = await checkItemInCookieCartProductId(id);

                    //console.log(cookieCartRes);

                    if (cookieCartRes.or) {
                        setCokAdd(true);
                    }

                } catch (error) {

                    //console.log(error);

                }
            }


            return setld(false);



        })();

        function getSingleByProductId(id) {

            return new Promise(async (resolve, reject) => {

                try {
                    const { data } = await singleProduct(id);
                    return resolve(data);
                } catch (error) {
                    return reject(error);

                }
            })

        }


        function checkItemInCookieCartProductId(id) {

            return new Promise(async (resolve, reject) => {

                try {
                    const { data } = await checkItemInCartCookie(id);
                    return resolve(data);
                } catch (error) {
                    return reject(error);

                }
            })

        }


        function checkItemInCustomerCartProductId(id) {

            return new Promise(async (resolve, reject) => {

                try {
                    const { data } = await checkItemInCustomerCart(id);
                    return resolve(data);
                } catch (error) {
                    return reject(error);

                }
            })

        }

    }, [id, isAuth]);

    return (
        <>
            <div>


                {
                    ld ? <MyLoader></MyLoader> : <>


                        <Container>

                            <Typography variant='h6' style={{ marginTop: "32px", marginBottom: "32px", textAlign: "center", textTransform: 'capitalize' }}>
                                welcome to product details page
                            </Typography>
                        </Container>

                        <Container>
                            <Grid container alignItems='center' spacing={3}>

                                <Grid item sm xs={12} className={classes.f1}>

                                    <ProductSilder items={pr.prImages}></ProductSilder>

                                </Grid>

                                <Grid item xs={12} sm style={{ textTransform: 'capitalize' }} className={classes.f2}>
                                    {
                                        parseInt(pr.discount) === 0 ? <></> : <Typography variant='subtitle2' style={{ color: 'red', marginBottom: "4px" }} >
                                            {parseInt(pr.discount)}% off
                                        </Typography>
                                    }

                                    <Box marginBottom={"8px"}>

                                        <Typography style={{ color: 'blue' }}>
                                            {pr.brand}
                                        </Typography>

                                    </Box>
                                    <Box marginBottom={"8px"} >
                                        <Typography variant='h6' className={classes.f3}> {pr.name}</Typography>
                                    </Box>

                                    <Grid container spacing={2} style={{ marginBottom: "8px" }}>

                                        <Grid item >
                                            <Typography variant='subtitle2' >
                                                Price <span>
                                                    <i className="fa fa-inr" aria-hidden="true"></i> {pr.spInGst}
                                                </span>
                                            </Typography>
                                        </Grid>
                                        <Grid item >
                                            <Typography variant='subtitle2' >

                                                MRP  <span style={{textDecoration:'line-through'}}>
                                                    <i className="fa fa-inr" aria-hidden="true"></i> {pr.mrp}
                                                </span>
                                            </Typography>

                                        </Grid>
                                        <Grid item >
                                            <Typography variant='subtitle2' style={{color:"blue"}}>
                                                you save <span>
                                                    <i className="fa fa-inr" aria-hidden="true"></i> {pr.mrp - pr.spInGst}
                                                </span>

                                            </Typography>
                                        </Grid>
                                    </Grid>


                                    <Box>
                                        <Box marginBottom={"8px"} >
                                            <Typography className={classes.f4}>
                                                {pr.deliveryCharge === 0 ? <span>
                                                    FREE Delivery by StoreClubs
                                                </span> : <span>
                                                    Delivery Charge   {pr.deliveryCharge}
                                                </span>}
                                            </Typography>
                                        </Box>
                                        <Box marginBottom={"8px"} >
                                            <Typography className={classes.f4}>
                                                {
                                                    moment(Date.now() + parseInt(pr.deliverTimeId.time)).format('LL')
                                                }
                                            </Typography>
                                        </Box>
                                    </Box>

                                    <Grid container justifyContent='space-between' alignItems='center' style={{ marginTop: "32px" }}>

                                        <Grid item >


                                            {
                                                isAuth ? userCart ? <Button variant='contained' color='primary' onClick={goToCart} >
                                                    go to  cart
                                                </Button> : <Button variant='contained' color='primary' onClick={addtocartUser}>
                                                    add to cart
                                                </Button> : cokadd ? <Button variant='contained' color='primary' onClick={goToCart}>
                                                    go to cart
                                                </Button> : <Button variant='contained' color='primary' onClick={addtocartCookie}>
                                                    Add to cart
                                                </Button>
                                            }
                                        </Grid>
                                        <Grid item >

                                            {
                                                isAuth ? <Button variant='contained' color='primary' onClick={buyNowToCart}>
                                                    Buy Now
                                                </Button> : <Button variant='contained' color='primary' onClick={buyNowToCookie}>
                                                    Buy Now
                                                </Button>
                                            }


                                        </Grid>

                                    </Grid>


                                </Grid>

                            </Grid>
                        </Container>

                        <Container style={{ textTransform: 'capitalize', marginTop: "32px" }}>



                            <Grid container >

                                <Grid item xs={12} sm>
                                    <Typography variant='subtitle2'>
                                        Product Details
                                    </Typography>

                                    <TableContainer style={{ marginTop: "8px", maxWidth: "350px", marginBottom: "24px" }}>

                                        <Table>
                                            <TableHead>
                                                {
                                                    pr.productMap.map((i) => {
                                                        return (<TableRow key={i._id}>

                                                            <TableCell>
                                                                {i.mykey}
                                                            </TableCell>
                                                            <TableCell>
                                                                {i.myvalue}
                                                            </TableCell>

                                                        </TableRow>)
                                                    })
                                                }
                                            </TableHead>
                                        </Table>
                                    </TableContainer>
                                </Grid>

                                <Grid item sm={5} xs={12}>

                                    <Typography variant='subtitle2' style={{ marginBottom: "8px" }}>
                                        About The Item
                                    </Typography>

                                    {
                                        pr.aboutItem.map((i) => {

                                            return (<Typography key={i._id} style={{ marginBottom: "8px" }}>
                                                {i.des}
                                            </Typography>)
                                        })
                                    }
                                </Grid>

                            </Grid>


                            <Typography variant='h6' style={{ marginBottom: "8px" }}>

                            </Typography>




                        </Container>
                    </>
                }




            </div>


        </>
    )
}
