import React, { } from 'react';

import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';

export default function HorizontalLinearStepper({ st }) {

    return (
        <div>

            <Stepper activeStep={st}>
                <Step>
                    <StepLabel>
                        Step of Address
                    </StepLabel>
                </Step>
                <Step>
                    <StepLabel>
                        Step Of OrderSummary
                    </StepLabel>
                </Step>
                
            </Stepper>
           
        </div >
    );
}
