import { Container, Grid, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { useParams } from 'react-router-dom';

import MyLoader from '../../components/MyLoading/MyLoader';


import { getSubcaterbyparentId } from '../../http/index';

export default function AllSubCatergory() {

    const { id } = useParams();
    const [d, setD] = useState([]);
    const [ld, setld] = useState(true);

    useEffect(() => {

        (async () => {

            try {

                const { data } = await getSubcaterbyparentId(id);

                //console.log(data);


                if (data.or) {

                    setD(data.result);
                }

            } catch (error) {

                //console.log(error);

            }

            return setld(false)
        })()

    }, [id]);

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container style={{ margin: "32px 0px", textAlign: 'center', textTransform: 'capitalize' }}>
                        <Typography variant='h6'>Welcome to All subcatergory page </Typography>
                    </Container>

                    <Container>

                        <Grid container spacing={4} style={{ textTransform: 'capitalize', marginTop: "28px" }}>

                            {
                                d.map((i) => {
                                    return (<Grid key={i._id} xs={6} sm={3} item container justifyContent='center' alignItems='center'>

                                        <Grid item >
                                            <Link to={`/subcatergory/${i._id}`} style={{ display: 'block', textDecoration: 'none', color: "black" }}>


                                                <Grid container justifyContent='center'>

                                                    <Grid item>
                                                        <img src={i.subcatergoryimage} alt={i.name} style={{ maxWidth: "100%", maxHeight:"150px" }}></img>
                                                    </Grid>

                                                </Grid>

                                                <Typography variant='subtitle2' style={{ textAlign:'center',marginTop: "8px" }}>{i.name}</Typography>
                                            </Link>

                                        </Grid>

                                    </Grid>)
                                })
                            }

                        </Grid>
                    </Container>

                </>
            }


        </div>
    )
}
