import { Button, Container, Paper, Typography, Grid, } from '@material-ui/core';
import React, { useEffect, useState } from 'react';

import { useHistory } from 'react-router-dom';

import MyBtn from '../MyCom/MyBtn';
import moment from 'moment';

import MyLoader from '../MyLoading/MyLoader';

import { registerOrder, getAllCartCustomer, getuserProfile, getDelivertimeforOrderSummary } from '../../http/index';

export default function StepOfOrderSummary() {

    const [ld, setld] = useState(true);

    const [orstatus, setorstatus] = useState(false);

    const [cr, setCr] = useState([]);

    const [profile, setProfile] = useState({ fullAddress: {}, pincodeId: {} });

    const [mydelivery, setmydelivery] = useState({ time: 0 });


    function goorder() {

        return history.push('/orders');

    }

    const history = useHistory();

    async function submit() {

        //console.log("submit button clicked");

        let ar = cr.map((i) => {

            return i.productId;
        });

        //console.log(ar);

        const mydata = {
            products: ar,
        };

        try {

            const { data } = await registerOrder(mydata);

            //console.log(data);

            if (data.or) {

                return setorstatus(true);

            }

        } catch (error) {

            //console.log(error);

        }




    }

    useEffect(() => {

        //  console.log("useeffect calling for ordersummary page");

        (async () => {

            //call customer profile then cart

            try {
                const prdata = await getUserProfileInOrderSummary();

                // console.log(prdata);

                if (prdata.or) {

                    setProfile(prdata.result);

                }

            } catch (error) {

                // console.log(error);

            }

            try {
                const crdata = await getCartDataForOrderSummary();

                //console.log(crdata);

                if (crdata.or) {

                    setCr(crdata.result);

                }

            } catch (error) {

                //console.log(error);

            }

            //get deliverTime by rate 

            try {
                const deliverdata = await getDeliverForOrderSummary();

                // console.log(deliverdata);

                if (deliverdata.or) {
                    setmydelivery(deliverdata.result);

                }

            } catch (error) {

                //console.log(error);

            }

            return setld(false);

        })();


        function getCartDataForOrderSummary() {


            return new Promise(async (resolve, reject) => {

                try {
                    const { data } = await getAllCartCustomer();

                    return resolve(data);

                } catch (error) {

                    return reject(error);

                }
            });
        }

        function getUserProfileInOrderSummary() {

            return new Promise(async (resolve, reject) => {

                try {
                    const { data } = await getuserProfile();

                    return resolve(data);

                } catch (error) {

                    return reject(error);

                }
            })


        }



        function getDeliverForOrderSummary() {

            return new Promise(async (resolve, reject) => {

                try {

                    const { data } = await getDelivertimeforOrderSummary();

                    return resolve(data);
                } catch (error) {

                    return reject(error);
                }
            });

        }


    }, []);

    return (
        <>

            {
                ld ? <MyLoader></MyLoader> : <>

                    <div style={{ marginTop: "24px" }}>

                        {
                            cr.length === 0 ? <Container>

                                <Grid container justifyContent='center'>

                                    <Grid item >
                                        <MyBtn path={'/'} n='continue shopping'>
                                        </MyBtn>
                                    </Grid>

                                </Grid>

                            </Container> : <div>

                                {
                                    orstatus ? <>
                                        <Container>
                                            <Grid container justifyContent='center' alignItems='center'>

                                                <Grid item >

                                                    <Typography style={{ color: "green", marginBottom: "24px" }}>
                                                        Order Placed Sucessfully Done
                                                    </Typography>

                                                    <Button color='primary' variant='contained' onClick={goorder}>
                                                        go to orders
                                                    </Button>

                                                </Grid>

                                            </Grid>
                                        </Container>
                                    </> : <>

                                        <Container>

                                            <Grid container alignItems='center' style={{ textTransform: 'capitalize', marginBottom: "16px" }} >
                                                <Grid item sm xs={12} style={{ marginBottom: "16px" }}>
                                                    <Typography variant='h6' style={{ marginBottom: "8px" }}>
                                                        {profile.name}
                                                    </Typography>
                                                    <Typography >
                                                        phone number  {profile.phone}
                                                    </Typography>
                                                </Grid>
                                                <Grid item xs={12} sm={3} >
                                                    <Typography variant="subtitle2" style={{ marginBottom: "8px" }}>
                                                        deliver to : {profile.address1} {" "}
                                                    </Typography>
                                                    <Typography variant="subtitle2" style={{ marginBottom: "8px" }}>
                                                        {profile.fullAddress.nameOfCity} {" "} {profile.fullAddress.stateOfCity} {" "} {profile.fullAddress.countryOfCity}  {" "} {profile.pincodeId.pincode}
                                                    </Typography>
                                                    <Typography variant="button" style={{ marginBottom: "8px" }}>
                                                        delivery date   {
                                                            moment(Date.now() + parseInt(mydelivery.time)).format('LLL')
                                                        }
                                                    </Typography>
                                                </Grid>
                                            </Grid>



                                        </Container>

                                        <Container>
                                            {
                                                cr.map((i) => {

                                                    return (
                                                        <Paper key={i._id} style={{ marginBottom: "24px", padding: "16px 4px", textTransform: 'capitalize' }}>

                                                            <Grid container spacing={1} alignItems='center'>

                                                                <Grid item container sm={2} xs={3} justifyContent='center' alignItems='center'>
                                                                    <Grid item >
                                                                        <img src={i.productImage} alt={i.name} style={{ maxWidth: "100%", height: "100px" }}>
                                                                        </img>
                                                                    </Grid>

                                                                </Grid>
                                                                <Grid item xs>
                                                                    <Typography variant='subtitle2'>
                                                                        {i.name}
                                                                    </Typography>
                                                                    <Typography>
                                                                        total quantity {i.quantity}
                                                                    </Typography>
                                                                    <Typography>
                                                                        total amount <i className="fa fa-inr" aria-hidden="true"></i>  {i.totalamount}
                                                                    </Typography>
                                                                    <Typography>
                                                                        toatal devlivery <i className="fa fa-inr" aria-hidden="true"></i>  {i.totaldelivery}
                                                                    </Typography>
                                                                </Grid>
                                                            </Grid>




                                                        </Paper>
                                                    )
                                                })
                                            }
                                        </Container>

                                        <Container style={{ textTransform: 'capitalize' }}>
                                            <Typography variant='h6' style={{ marginBottom: "16px" }}>
                                                OrderSummary
                                            </Typography>
                                            <Typography style={{ marginBottom: "8px" }}>
                                                total quantity   {cr.reduce((acc, i) => {
                                                    return acc + i.quantity;
                                                }, 0)

                                                }
                                            </Typography>
                                            <Typography style={{ marginBottom: "8px" }}>
                                                totoal amount  <i className="fa fa-inr" aria-hidden="true"></i>  {
                                                    cr.reduce((acc, i) => {
                                                        return acc + i.totalamount
                                                    }, 0)
                                                }

                                            </Typography>
                                            <Typography style={{ marginBottom: "8px" }}>
                                                total delivey cost <i className="fa fa-inr" aria-hidden="true"></i>   {
                                                    cr.reduce((acc, i) => {
                                                        return acc + i.totaldelivery
                                                    }, 0)
                                                }
                                            </Typography>
                                            <Typography variant='h6' style={{ marginBottom: "8px" }}>
                                                Total amount of Order  <i className="fa fa-inr" aria-hidden="true"></i>  {
                                                    cr.reduce((acc, i) => {
                                                        return acc + i.totaldelivery + i.totalamount;
                                                    }, 0)
                                                }
                                            </Typography>

                                            <Button onClick={submit} variant='contained' color='primary' style={{ marginTop: "16px", marginBottom: "16px" }}>
                                                Placed Order
                                            </Button>
                                        </Container>

                                    </>
                                }



                            </div>
                        }

                    </div>
                </>
            }

        </>
    )
}
