import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom';

import { getAllServices } from '../../http/index';

import { Button, Container, Grid, Typography } from '@material-ui/core';


import MyLoader from '../../components/MyLoading/MyLoader';

export default function AllService() {

  const [ld, setld] = useState(true);

  const [md, setmd] = useState([]);

  const history = useHistory();


  function st() {

    history.push('/bookservice');

  }

  useEffect(() => {

    (async () => {

      try {

        const { data } = await getAllServices();

        //console.log(data);

        if (data.or) {

          setmd(data.result);

        }

      } catch (error) {

        //console.log(error);

      }

      return setld(false);

    })();

  }, []);



  return (
    <div>


      {
        ld ? <MyLoader></MyLoader> : <>

          <Container style={{ margin: "32px 0px", textAlign: 'center', textTransform: 'capitalize' }}>

            <Typography variant='h6'>
              All services page list
            </Typography>
          </Container>


          <Container>

            <Grid container spacing={2} >

              {
                md.map((i) => {

                  return (<Grid key={i._id} item xs={6} sm={3}>

                    <Grid container justifyContent='center'>
                      <Grid item >
                        <img src={i.workimage} alt={i.nameofwork} style={{ maxWidth: "100%", height: "100px" }}></img>
                      </Grid>
                    </Grid>

                    <Typography variant='subtitle2' style={{ marginTop: "16px", textAlign: 'center', textTransform: 'capitalize' }}>
                      {i.nameofwork}
                    </Typography>
                  </Grid>)
                })
              }

            </Grid>

            <Grid container justifyContent='center' alignItems='center' style={{ marginTop: "24px" }}>
              <Grid item >

                <Button variant='contained' color='primary' onClick={st}>
                Order Now By List
                </Button>
              </Grid>

            </Grid>
          </Container>

        </>
      }



    </div>
  )
}
