import axios from 'axios';

const api = axios.create({
    baseURL: 'https://storeclubs.com',
    withCredentials: true,
    headers: {
        'Content-type': 'application/json',
        Accept: 'application/json',
    }
});


const config = {
    headers: {
        "Content-Type": "multipart/form-data",
    },
};


// list of api
//auth
export const sendOtp = (data) => api.post('/api/customer/send-otp', data);

export const verifyOtp = (data) => api.post('/api/customer/verify-otp', data);

export const logoutCus = () => api.post('/api/customer/logout');

// activate

export const activateProfile = (data) => api.post('/api/customer/activate/user', data);

//profile 

export const getuserProfile = () => api.get('/api/customer/activate/profile');

//product
export const searchProduct = (data) => api.get('/api/customer/search', {
    params: {
        qry: data,
    }
});

export const singleProduct = (data) => api.get(`/api/customer/product/${data}`);

//cookie
export const addToCartCookie = (data) => api.post('/api/customer/cookie/add-cookie', data);

export const getAllCartCookie = () => api.get('/api/customer/cookie/get-cookie');

export const deletecartCookie = (data) => api.post('/api/customer/cookie/del-cookie', data);

export const updatecartCookie = (data) => api.post('/api/customer/cookie/update-cookie', data);

export const checkItemInCartCookie = (data) => api.get(`/api/customer/cookie/check/${data}`);//checking

// cutomer cart
export const getAllCartCustomer = () => api.get('/api/customer/cus-cart/get-all');

export const getSingleCartCustomer = (data) => api.get(`/api/customer/cus-cart/single/${data}`);

export const addItemToCustomerCart = (data) => api.post('/api/customer/cus-cart/add', data);

export const updateCustomerCart = (data) => api.post('/api/customer/cus-cart/update', data);

export const deleteCustomerCart = (data) => api.post('/api/customer/cus-cart/delete', data);

export const checkItemInCustomerCart = (data) => api.get(`/api/customer/cus-cart/check/${data}`);

//form address 
export const getAddressForForm = () => api.get('/api/customer/address/form');

//orders 

export const registerOrder = (data) => api.post('/api/customer/order/add', data);
export const getAllOrderOfCustomer = () => api.get('/api/customer/order/get-all');
export const getSingleOrder = (data) => api.get(`/api/customer/order/single/${data}`);

export const cancelCustomerOrder = (data) => api.post('/api/customer/order/cancel', data);

export const getCustomerDataEdit = () => api.get('/api/customer/edit-profile/get');

export const addCustomerDataEdit = (data) => api.post('/api/customer/edit-profile/add', data);

//get time 
export const getDelivertimeforOrderSummary = () => api.get('/api/customer/delivery/single');


//home page data 

export const getAllCatergoryforHome = () => api.get('/api/customer/catergory/all');

export const getTopProducts = () => api.get('/api/customer/home/topproducts/all');

export const getSubcaterbyparentId = (data) => api.get(`/api/customer/subcatergory/all/${data}`);

export const getSubProdutsbysubcateroryid = (data) => api.get(`/api/customer/product/all/${data}`);

export const getallshopbycatergory = () => api.get('/api/customer/shopbycatergory/all');


//works routes
export const getAllServices = () => api.get('/api/customer/wk/works/all');
export const registerCustomerWork = (data) => api.post('/api/customer/wk/work/add', data,config);

export const getAllCustomerService = () => api.get('/api/customer/wk/service/all');

export const getSingleCustomerService = (data) => api.get(`/api/customer/wk/service/single/${data}`);



// interpertors
api.interceptors.response.use((config) => {
    return config;
}, async (error) => {


    const originalRequest = error.config;


    if (error.response.status === 401 && error.config && !error.config._isRetry) {

        //console.log("inertrcerpotors call only if status 401");

        originalRequest._isRetry = true;
        try {
            await axios.get('https://storeclubs.com/api/customer/refresh', { withCredentials: true });

            return api.request(originalRequest);

        } catch (error) {

            // console.error(error);
        }
    }
    throw error;

});





export default api;

