import { Grid, Typography, Button } from '@material-ui/core';
import React from 'react';

export default function UserProfile({ data, onNext }) {

    
    function nt() {

        return onNext();
    }
    return (
        <div>

            <Grid container justifyContent='center' alignItems='center' style={{ marginTop: "16px", marginBottom: "16px", textTransform: 'capitalize' }}>

                <Grid item>
                    <Typography variant='h6' style={{ marginBottom: "12px" }}>
                        {data.name}
                    </Typography>
                    <Typography style={{ marginBottom: "12px" }}>
                        phone Number  {data.phone}
                    </Typography>
                    <Typography style={{ marginBottom: "12px" }}>
                        address {data.address1}
                    </Typography>
                    <Typography style={{ marginBottom: "12px" }}>
                        full address {data.fullAddress.nameOfCity} {" "} {data.fullAddress.stateOfCity}
                        {" "} {data.fullAddress.countryOfCity}
                    </Typography>
                    <Typography style={{ marginBottom: "32px" }}>
                        pincode {data.pincodeId.pincode}
                    </Typography>

                    <Button onClick={nt} variant='contained' color='primary' >
                        Next 
                    </Button>

                </Grid>

            </Grid>



        </div>
    )
}
