import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';

import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

import { Container, Grid, IconButton, TextField, Typography } from '@material-ui/core';
import { useHistory } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { setLogout, } from '../../store/customerSlice';
import { logoutCus } from '../../http/index';

import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';


import { setSearch, } from '../../store/searchSlice';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    offset: theme.mixins.toolbar,
    first: {

        [theme.breakpoints.down('xs')]: {
            display: "none",
        }
    },
    second: {
        display: 'none',
        [theme.breakpoints.down('xs')]: {
            display: "flex",
        }
    },
    last: {
        [theme.breakpoints.down('xs')]: {
            height: "58px",
        }
    },
    ser: {
        [theme.breakpoints.down('xs')]: {
            marginBottom: "12px",
            marginTop: "8px",
        }
    },
    fheader: {
        [theme.breakpoints.down('xs')]: {
            display: "none",
        }
    },
    dfheader: {
        display: "none",
        [theme.breakpoints.down('xs')]: {
            display: "block",
        }
    },

}));


export default function ButtonAppBar() {
    const classes = useStyles();

    const { isAuth, customer } = useSelector((state) => state.auth);


    const disptach = useDispatch();

    const serDisptach = useDispatch();


    let [t, setT] = useState('');

    const history = useHistory();

    function submit() {


        if (!t) {
            return;
        }

        serDisptach(setSearch(t));
        history.push(`/search?qry=${t}`);

    }

    function vcart() {
        history.push('/viewcart')
    }


    function h1() {

        history.push('/about')
    }

    function h2() {

        history.push('/contact')
    }

    function h3() {

        history.push('/allcatergory')
    }

    function h4() {

        history.push('/all/services');
    }



    async function logout() {

        //console.log("logut button clicked");

        try {

            const { data } = await logoutCus();

            //console.log(data);

            if (!data.auth) {
                // logout suceesfuly

                disptach(setLogout(null));

            }

        } catch (error) {

            // console.log(error);


        }
    }






    return (
        <div className={classes.root}>
            <Container>


                <AppBar style={{ backgroundColor: 'white', color: 'black' }}>

                    <Container maxWidth='xl' style={{ margin: "8px 0px",backgroundColor: "#0070ba" ,color:"white" }}>
                        <Grid container alignItems='center' justifyContent='space-between' >
                            <Grid item sm>
                                <Typography variant='subtitle2' >CAll US : +91 9131683474</Typography>
                            </Grid>
                            <Grid item >
                                <Button onClick={h1} className={classes.fheader} style={{ marginRight: "8px",color:"white" }} size="small"  >About Us</Button>
                                <Button onClick={h2} className={classes.fheader} style={{ marginRight: "8px",color:"white" }} size="small" >Contact Us</Button>
                                <Button onClick={h3} className={classes.fheader} style={{ marginRight: "8px",color:"white" }} size="small" >Shop By Catergory</Button>

                                <Button onClick={h4} className={classes.dfheader} size="small" style={{ color:"white" }} > all services</Button>
                            </Grid>
                        </Grid>
                    </Container>

                    <Toolbar>

                        <Grid container alignItems='center'>

                            <Grid item xs={12} sm={2} container alignItems='center'>
                                <Grid item xs >
                                    <Button onClick={() => {
                                        history.push('/');
                                    }} color='primary'>
                                        StoreClubs
                                    </Button>
                                </Grid>
                                <Grid item className={classes.second}  >


                                    {
                                        isAuth ? <MyAccount logout={logout}></MyAccount> : <MyBtnLink to='/login'>
                                            Login
                                        </MyBtnLink>
                                    }




                                    <IconButton color="primary" onClick={vcart} >
                                        <ShoppingCartIcon></ShoppingCartIcon>
                                    </IconButton>
                                </Grid>

                            </Grid>
                            <Grid item xs={12} sm container justifyContent='space-around' alignItems='center'>
                                <Grid item className={classes.ser} xs={12} sm={4} container spacing={1} alignItems='center'>
                                    <Grid item xs >
                                        <TextField label='Search Products' value={t} onChange={(e) => {
                                            setT(e.target.value);
                                        }} variant='outlined' size='small' style={{ width: "100%" }}></TextField>
                                    </Grid>

                                    <Grid item xs={4} sm={2}>
                                        <Button onClick={submit} variant='outlined' color='primary'>
                                            search
                                        </Button>

                                    </Grid>

                                </Grid>
                                <Grid item xs={12} sm={6} className={classes.first} container spacing={3} alignItems='center'>

                                    <Grid item sm container alignItems='center' spacing={2}>
                                        <Grid item >
                                            {
                                                isAuth ? <Typography style={{ textTransform: 'capitalize' }}>
                                                    {
                                                        customer.activated && customer.name
                                                    }
                                                </Typography> : <MyBtnLink to='/login'>
                                                    Login
                                                </MyBtnLink>
                                            }
                                        </Grid>
                                        <Grid item >
                                            {
                                                isAuth && <MyAccount logout={logout}></MyAccount>
                                            }
                                        </Grid>


                                    </Grid>
                                    <Grid item >
                                        <MyBtnLink to='/all/services'>
                                            ALL Services
                                        </MyBtnLink>
                                    </Grid>

                                    <Grid item >
                                        <MyBtnLink to='/viewcart'>
                                            cart
                                        </MyBtnLink>
                                    </Grid>

                                </Grid>
                            </Grid>
                        </Grid>
                    </Toolbar>
                </AppBar>
                <div className={classes.last} style={{ marginBottom: "48px" }}>

                </div>
            </Container>

            <div className={classes.offset} />
        </div>
    );
}



function MyBtnLink({ to, children }) {
    const history = useHistory();
    function s() {
        history.push(`${to}`);

    }
    return (
        <Button onClick={s} color='primary'>
            {children}
        </Button>
    )
}




function MyAccount({ logout }) {

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };
    const history = useHistory();
    function mProfile() {
        setAnchorEl(null);
        return history.push('/profile');

    }
    function mOrder() {
        setAnchorEl(null);
        return history.push('/orders');

    }

    function mServices() {
        setAnchorEl(null);
        return history.push('/myservices');
    }

    function handelLogout() {
        setAnchorEl(null);
        return logout();
    }


    return (
        <>
            <Button aria-controls="account" aria-haspopup="true" color='primary' onClick={handleClick} >
                My Account
            </Button>
            <Menu
                id="account"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                <MenuItem onClick={mProfile} component="button" style={{ marginBottom: "16px" }} >Profile</MenuItem>
                <MenuItem onClick={mOrder} component="button" style={{ marginBottom: "16px" }} >Orders</MenuItem>
                <MenuItem onClick={mServices} component="button" style={{ marginBottom: "16px" }} >Order-Services</MenuItem>
                <MenuItem onClick={handelLogout} component="button" style={{ marginBottom: "16px" }} >Logout</MenuItem>
            </Menu>
        </>)

}



