import axios from "axios";
import { useEffect } from "react";



export function useInitialCookie() {

    useEffect(()=>{

        //console.log("useeffect calling for initail cookie ");

        (async()=>{

            try {
                
                await axios.get('https://storeclubs.com/api/customer/cookie/set-cookie',{withCredentials:true});

                //console.log(data);

            } catch (error) {
                
               // console.log(error);
                
            }

        })();
    },[]);
    
}





