import { Container, Typography } from '@material-ui/core'
import React from 'react'

export default function TermsCond() {
    return (
        <div>

            <Container style={{ marginBottom: "32px", textTransform: 'capitalize' }}>
                <Typography variant='h6' style={{margin:"16px 0px"}}>
                    StoreClubs  Privacy Policy
                </Typography>

                <Typography>
                    We know that you care how information about you is used , and we appreciate your trust that we will do so carefully and sensibly. This Privacy Notice describes how StoreClubs  collect and process your personal information through storeclubs.com website that reference this Privacy Notice .
                </Typography>

                <Typography variant='subtitle2' style={{marginTop:"16px",marginBottom:"8px"}}>
                    What Personal Information About Customers Does StoreClubs Collect?
                </Typography>
                <Typography>
                    We collect your personal information in order to provide and continually improve our products and services.
                </Typography>

                <Typography variant='subtitle2' style={{marginTop:"16px",marginBottom:"8px"}}>
                    For What Purposes Does StoreClubs Use Your Personal Information?
                </Typography>
                <Typography>
                    Purchase and delivery of products and services. We use your personal information to take and fulfill orders, deliver products and services, process payments, and communicate with you about orders, products and services, and promotional offers.
                </Typography>

                <Typography variant='subtitle2' style={{marginTop:"16px",marginBottom:"8px"}}>
                    What About Cookies and Other Identifiers?
                </Typography>
                <Typography>
                    To enable our systems to recognize your browser or device and to provide and improve StoreClubs Services, we use cookies and other identifiers.
                </Typography>

                <Typography variant='subtitle2' style={{marginTop:"16px",marginBottom:"8px"}}>
                    Does StoreClubs Share Your Personal Information?
                </Typography>
                <Typography>
                    Transactions involving Third Parties for payments
                </Typography>
                <Typography variant='subtitle2' style={{marginTop:"16px",marginBottom:"8px"}}>
                    How Secure Is Information About Me?
                </Typography>
                <Typography>
                    We design our systems with your security and privacy in mind.
                </Typography>
            </Container>
        </div>

    )
}
