import { Box, Button, Container, Grid, TextField, Typography } from '@material-ui/core';
import React, { useState } from 'react';

import { useDispatch, useSelector } from 'react-redux';

import { verifyOtp } from '../../http/index';

import { setAuth } from '../../store/customerSlice';


export default function SetpOtp() {

    const [otp, setOtp] = useState('');
    const dispatch = useDispatch();
    const { phone, hash } = useSelector((state) => state.auth.otp);
    const [er, setEr] = useState(false);

    async function submit(e) {

        e.preventDefault();
        //console.log("button clicekd");


        if (!otp) {
            //console.log("valdtion error ");
            return setEr(true);

        }

        if (otp.length < 4) {
            return setEr(true);

        }

        try {

            const { data } = await verifyOtp({ phone, hash, otp });

            //console.log(data); //{auth:true,customerDto:{}}

            if (data.auth) {

                dispatch(setAuth({ customer: data.customerDto }));

            }


        } catch (error) {

            //console.log("error in verfifying otp ");
            setEr(true);

            //console.log(error);

        }


    }



    return (
        <div>


            <Container>
                <form>
                    <Grid container alignItems='center' justifyContent='center' >

                        <Grid item >
                            <Box marginBottom={"16px"}>
                                <Typography>
                                    Enter your otp this phone number {phone}
                                </Typography>
                                <Typography variant='subtitle2' style={{marginTop:"8px",textTransform:'capitalize'}}>
                                    Otp is Valid for 2 minute only
                                </Typography>

                            </Box>
                            {
                                er && <Box marginBottom={"16px"}>
                                    <Typography style={{ textTransform: 'capitalize', color: 'red' }}>
                                        Invalid Otp
                                    </Typography>

                                </Box>
                            }

                            <Box marginBottom={"32px"}>
                                <TextField type={'text'} variant='outlined' size="small" value={otp} onChange={(e) => {
                                    setOtp(e.target.value);

                                }}>

                                </TextField>
                            </Box>
                            <Box>
                                <Button onClick={submit} variant='contained' color='primary'>
                                    Submit
                                </Button>
                            </Box>
                        </Grid>

                    </Grid>

                </form>
            </Container>


        </div>
    )
}
