import React, { useEffect, useState } from 'react';

import { getAllCartCustomer, updateCustomerCart, deleteCustomerCart } from '../../http/index';
import { Button, Container, Paper, Typography, makeStyles, Grid, TextField, MenuItem, Box } from '@material-ui/core';

import MyLoader from '../../components/MyLoading/MyLoader';

import { useHistory } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({

    f1: {
        marginBottom: "16px",
        [theme.breakpoints.down('sm')]: {
            fontSize: "16px",
            marginBottom: "8px"
        },
    },
    f2: {
        marginBottom: "16px",
        [theme.breakpoints.down('sm')]: {
            fontSize: "14px",
            marginBottom: "8px"
        },
    }


}));

export default function CustomerCartPage() {

    const [ld, setld] = useState(true);

    const classes = useStyles();
    const [pr, setPr] = useState([]);

    const history = useHistory();


    function nextStep() {

        history.push('/viewcheckout');

    }

    function updateChangeSate(data) {

        return setPr(data);
    }

    function deleteChangeState(data) {
        return setPr(data);

    }

    function MyShoping() {

        history.push('/');
    }

    useEffect(() => {

        //console.log("use effect callling for cutomer cart");

        (async () => {

            try {
                const { data } = await getAllCartCustomer();

                //console.log(data);

                if (data.or) {

                    setPr(data.result);
                }

            } catch (error) {

                //console.log(error);

            }

            return setld(false);


        })();

    }, [])





    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>

                        <Typography variant='h6' style={{ marginTop: "32px", marginBottom: "32px", textAlign: 'center', textTransform: 'capitalize' }}>
                            welcome to CustomerCartPage page
                        </Typography>
                    </Container>


                    {
                        pr.length === 0 ? <Container>

                            <Grid container justifyContent='center' alignItems='center' style={{ textTransform: 'capitalize' }}>

                                <Grid item >
                                    <Typography variant='h6' style={{ marginBottom: "16px" }}>
                                        Cart is empty
                                    </Typography>
                                    <Button onClick={MyShoping} color='primary'>
                                        Continue Shopping
                                    </Button>
                                </Grid>

                            </Grid>

                        </Container> : <div>

                            <Container>




                                {
                                    pr.map((i) => {

                                        return (
                                            <Paper key={i.productId} style={{ textTransform: 'capitalize', paddingBottom: "16px", paddingTop: "8px", marginBottom: "20px" }}>

                                                <Grid container>

                                                    <Grid item xs container sm={2} justifyContent='center' alignItems='center'>

                                                        <Grid item >
                                                            <img src={i.productImage} alt={i.name} style={{ maxWidth: "100%", height: "200px" }}></img>
                                                        </Grid>
                                                    </Grid>

                                                    <Grid item sm xs>
                                                        <Typography variant='h6' className={classes.f1} >
                                                            {i.name}
                                                        </Typography>
                                                       
                                                        <Typography className={classes.f2} >

                                                            qutanity {i.quantity}
                                                        </Typography>
                                                        <Typography variant='h6' className={classes.f1} >

                                                            amount  <i className="fa fa-inr" aria-hidden="true"></i> {i.totalamount}
                                                        </Typography>
                                                        <Typography className={classes.f2} >
                                                            deliveryCharge  <i className="fa fa-inr" aria-hidden="true"></i> {i.totaldelivery}
                                                        </Typography>

                                                        <SelectUpdate id={i.productId} updateChangeSate={updateChangeSate}></SelectUpdate>

                                                        <DeleteButton id={i.productId} deleteChangeState={deleteChangeState}></DeleteButton>

                                                    </Grid>

                                                </Grid>

                                            </Paper>
                                        )
                                    })
                                }


                            </Container>
                            <Container>


                                <Grid container style={{ textTransform: 'capitalize' }}>

                                    <Grid item >
                                        <Typography style={{ marginBottom: "8px" }}>
                                            total quantity     {
                                                pr.reduce((acc, i) => {

                                                    return i.quantity + acc;

                                                }, 0)
                                            }
                                        </Typography>
                                        <Typography style={{ marginBottom: "8px" }}>
                                            total amount <i className="fa fa-inr" aria-hidden="true"></i>     {
                                                pr.reduce((acc, i) => {

                                                    return acc + i.totalamount

                                                }, 0)
                                            }
                                        </Typography>



                                        <Typography style={{ marginBottom: "8px" }}>
                                            Total deliveryCharge  <i className="fa fa-inr" aria-hidden="true"></i>  {
                                                pr.reduce((acc, i) => {

                                                    return acc + i.totaldelivery;

                                                }, 0)
                                            }
                                        </Typography>

                                        <Typography variant='h6' style={{ marginBottom: "8px" }}>
                                            total bill amount <i className="fa fa-inr" aria-hidden="true"></i>  {
                                                pr.reduce((acc, i) => {

                                                    return acc + i.totaldelivery + i.totalamount;

                                                }, 0)
                                            }
                                        </Typography>
                                        <Box margin={"16px 0px"}>
                                            <Button onClick={nextStep} variant='contained' color='primary'>
                                                OrderNow
                                            </Button>
                                        </Box>
                                    </Grid>

                                </Grid>

                            </Container>

                        </div>
                    }



                </>
            }



        </div>
    )
}


function DeleteButton({ id, deleteChangeState }) {


    async function del() {
       // console.log("del buttton clicked");
       // console.log(id);

        try {
            const { data } = await deleteCustomerCart({ productId: id });

           // console.log(data);

            if (data.or) {

                return deleteChangeState(data.result);
            }



        } catch (error) {

            //console.log(error);

        }
    }
    return (

        <Button onClick={del} variant='contained' color='primary'>
            Remove
        </Button>

    )

}




function SelectUpdate({ id, updateChangeSate }) {


    const [qty, setQty] = useState(1);

    async function updatecart(d) {

        //console.log("update function call");
        //console.log(d);

        //console.log(id);

        try {

            const { data } = await updateCustomerCart({ productId: id, quantity: parseInt(d) });

           // console.log(data);

            if (data.or) {

                return updateChangeSate(data.result);
            }

        } catch (error) {

            //console.log(error);
        }

    }

    return (
        <div>
            <Grid container spacing={3} alignItems='center' style={{ marginBottom: "16px" }}>
                <Grid item >
                    <Typography>
                        Select Quantity
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={4}>
                    <TextField select label='Select Quanity' value={qty} onChange={(e) => {
                        setQty(e.target.value);
                        updatecart(e.target.value);

                    }} variant='outlined' size='small' style={{ width: "100%" }}>
                        <MenuItem value={1} > 1
                        </MenuItem>
                        <MenuItem value={2}> 2
                        </MenuItem>
                        <MenuItem value={3}> 3
                        </MenuItem>

                    </TextField>
                </Grid>
            </Grid>

        </div>
    )
}

