import { Box, Button, Container, Grid, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router';
import { getuserProfile } from '../../http/index';

import MyLoader from '../../components/MyLoading/MyLoader';

export default function ProfilePage() {

    //calling use effect for profilwe and edit profile

    const [d, setD] = useState({ pincodeId: {}, fullAddress: {} });

    const [ld, setld] = useState(true);


    const history = useHistory();

    function c() {

        history.push('/profile/edit');

    }
    useEffect(() => {

       // console.log("use effcect profile");

        (async () => {

            try {
                const { data } = await getuserProfile();

                //console.log(data);

                if (data.or) {

                    setD(data.result);

                }
            } catch (error) {
                
               // console.log(error);

            }

            return setld(false);
        })();
    }, [])

    return (
        <div>


            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>
                        <Grid container alignItems='center' style={{ marginTop: "32px", marginBottom: "32px" }}>

                            <Grid item xs >
                                <Typography variant='h6' style={{ textTransform: 'capitalize' }}>
                                    welcome to customer profile page
                                </Typography>
                            </Grid>
                            <Grid item >
                                <Button onClick={c} variant='contained' color='secondary'>
                                    edit profile
                                </Button>
                            </Grid>
                        </Grid>

                    </Container>

                    <Container>

                        <Grid container style={{ textTransform: 'capitalize' }}>

                            <Grid item >
                                <Box style={{ marginBottom: "16px" }}>
                                    <Typography>
                                        name     {d.name} sanjay
                                    </Typography>
                                </Box>

                                <Box style={{ marginBottom: "16px" }}>
                                    <Typography>
                                        phone      {d.phone}
                                    </Typography>
                                </Box>

                                <Box style={{ marginBottom: "16px" }}>
                                    <Typography>
                                        first address    {d.address1}
                                    </Typography>

                                </Box>
                                <Box style={{ marginBottom: "16px" }}>
                                    <Typography>
                                        second address    {d.address2}
                                    </Typography>
                                </Box>

                                <Box style={{ marginBottom: "16px" }}>
                                    <Typography>
                                        city      {d.fullAddress.nameOfCity} {" "} {d.fullAddress.stateOfCity} {" "} {d.fullAddress.countryOfCity}
                                    </Typography>

                                </Box>
                                <Box style={{ marginBottom: "16px" }}>
                                    <Typography>
                                        pincode   {d.pincodeId.pincode}
                                    </Typography>
                                </Box>
                            </Grid>

                        </Grid>


                    </Container>
                </>
            }

        </div>
    )
}
