import { Container, Typography } from '@material-ui/core';
import React from 'react';

import { useLocation } from 'react-router-dom';

import AllCheckout from '../AllCheckout/AllCheckout';

import SingleCheckout from '../SingleCheckout/SingleCheckout';


export default function DecideCheckoutPage() {

    let id;
   // console.log("decide checkout comp");

    const location = useLocation();

    const { search } = location;

    if (search) {
        //console.log(search);

        id = search.split("=")[1];// pr=100179723product
    }


    return (
        <div>

            <Container>
                <Typography variant='h6' style={{ marginBottom: "16px", marginTop: "32px", textAlign: 'center' }}>
                    Checkout Out Now
                </Typography>

            </Container>

            {
                search.includes("?pr=") ? <SingleCheckout id={id}></SingleCheckout> : <AllCheckout></AllCheckout>
            }
        </div>
    )
}

