import { Container, Grid, Typography, makeStyles, } from '@material-ui/core';
import moment from 'moment';
import React, { useEffect, useState } from 'react';

import { Link, } from 'react-router-dom';

import { searchProduct } from '../../http/index';
import MyLoader from '../../components/MyLoading/MyLoader';

import { useSelector } from 'react-redux';


const useStyles = makeStyles((theme) => ({

    first: {
        marginBottom: "16px",
        [theme.breakpoints.down('sm')]: {
            marginBottom: '8px',

        },

    },
    second: {
        marginBottom: "16px",
        [theme.breakpoints.down('sm')]: {
            marginBottom: '8px',
            fontSize: "12px",

        },

    },
    f1: {
        [theme.breakpoints.down('sm')]: {
            fontSize: "14px",

        },
    },
    f2: {
        [theme.breakpoints.down('sm')]: {
            fontSize: "12px",

        },
    },
    third: {
        [theme.breakpoints.down('sm')]: {
            marginLeft: "16px",

        },
    },
    l: {
        minHeight: "85vh",
        [theme.breakpoints.down('sm')]: {
            minHeight: "65vh",
        },
    },

}));



export default function ResultsofSearch() {

    const classes = useStyles();

    const [pr, setPr] = useState([{ deliverTimeId: { time: 0 } }]);



    const [ld, setld] = useState(true);

    const myser = useSelector((state) => state.sr.ser);


    useEffect(() => {

        // console.log("usef ffect callin for search");


        (async () => {

            // console.log(myser);

            if (myser) {

                try {

                    const { data } = await searchProduct(myser);

                    // console.log(data);

                    if (data.or) {
                        setPr(data.result);
                    }

                } catch (error) {
                    // console.log(error);
                }
            } else {

                //console.log("qry is empty");

                setPr([]);

            }


            return setld(false);


        })();

    }, [myser]);


    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>


                    <Container>
                        {
                            pr.length === 0 ? <Typography style={{ marginTop: "32px", marginBottom: "32px" }}>
                                No Products Found
                            </Typography> : <Typography style={{ marginTop: "32px", marginBottom: "32px" }}>
                                Total number of Products {pr.length}
                            </Typography>
                        }

                    </Container>
                    <Container>

                        {
                            pr.map((i) => {

                                return (<Grid container spacing={3} justifyContent='center' alignItems='center' key={i._id} style={{ textTransform: 'capitalize', marginBottom: "32px" }}>

                                    <Grid item xs sm={2} container justifyContent='center'>

                                        <Grid item >
                                            <Typography variant='subtitle2'>
                                                {
                                                    parseInt(i.discount) === 0 ? <></> : <>
                                                        <span style={{ color: "red" }}>
                                                            {parseInt(i.discount)}% off
                                                        </span>
                                                    </>
                                                }
                                            </Typography>
                                            <Link to={`/pr/${i._id}`} style={{ display: "block", textDecoration: "none" }} >
                                                <img src={i.mainImage} alt={i.name} style={{ maxWidth: "100%", }} ></img>
                                            </Link>

                                        </Grid>

                                    </Grid>

                                    <Grid item xs sm={6} container direction='column' >
                                        <Grid item className={classes.first}>


                                            <Link to={`/pr/${i._id}`} style={{ display: "block", textDecoration: "none", color: "black" }} >
                                                <Typography variant='h6' className={classes.f1}  >
                                                    {i.name}
                                                </Typography>
                                            </Link>

                                        </Grid>
                                        <Grid item container  style={{ marginBottom: "16px",marginTop:"4px",marginLeft:"4px" }}>
                                            <Grid item style={{ marginRight:"4px" }}>
                                                <Typography variant='subtitle2' >

                                                    Price <span>
                                                        <i className="fa fa-inr" aria-hidden="true"></i> {i.spInGst}
                                                    </span>
                                                </Typography>
                                            </Grid>
                                            <Grid item style={{ marginRight:"4px" }}>
                                                <Typography variant='subtitle2' >

                                                    MRP  <span style={{ textDecoration: "line-through" }}>
                                                        <i className="fa fa-inr" aria-hidden="true"></i> {i.mrp}
                                                    </span>
                                                </Typography>
                                            </Grid>
                                            <Grid item style={{ marginRight:"4px"}} >
                                                <Typography variant='subtitle2' style={{ color: "blue" }}>
                                                    you save <span>
                                                        <i className="fa fa-inr" aria-hidden="true"></i> {i.mrp - i.spInGst}
                                                    </span>
                                                </Typography>
                                            </Grid>

                                        </Grid>
                                        <Grid item >

                                            <Typography className={classes.second}>

                                                Get it by  {
                                                    moment(Date.now() + parseInt(i.deliverTimeId.time)).format("LL")
                                                }
                                            </Typography>
                                            {
                                                i.deliveryCharge === 0 ? <Typography className={classes.f2}>

                                                    FREE Delivery by StoreClubs
                                                </Typography> : <Typography className={classes.f2}>
                                                    Delivery Charge {i.deliveryCharge}
                                                </Typography>
                                            }

                                        </Grid>


                                    </Grid>

                                </Grid>)
                            })
                        }


                    </Container>


                </>
            }





        </div>
    )
}
