import { Button, Container, Paper, Typography, Grid, TextField, MenuItem, Box, makeStyles } from '@material-ui/core';
import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router';
import MyLoader from '../../components/MyLoading/MyLoader';

import { getAllCartCookie, deletecartCookie, updatecartCookie } from '../../http/index';

const useStyles = makeStyles((theme) => ({

    f1: {
        marginBottom: "16px",
        [theme.breakpoints.down('sm')]: {
            fontSize: "16px",
            marginBottom: "8px"
        },
    },
    f2: {
        marginBottom: "16px",
        [theme.breakpoints.down('sm')]: {
            fontSize: "14px",
            marginBottom: "8px"
        },
    }


}));

export default function CookieCart() {
    const [ld, setld] = useState(true);

    const classes = useStyles();

    const history = useHistory();

    const [pr, setPr] = useState([]);

    function checkout() {

        history.push('/viewcheckout?takecart=true');
    }


    function updateStatebyUpdate(data) {

        return setPr(data);

    }

    function deleteItemChangeState(data) {

        return setPr(data);
    }

    function MyShoping() {

        history.push('/');

    }
    useEffect(() => {

       // console.log("use effect for cookie items");

        (async () => {
            try {
                const { data } = await getAllCartCookie();

                //console.log(data);

                if (data.or) {
                    setPr(data.result);

                }

            } catch (error) {

                //console.log(error);

            }

            return setld(false);
        })();
    }, []);
    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>

                    <Container>
                        <Typography variant='h6' style={{ marginTop: "32px", marginBottom: "32px", textAlign: 'center', textTransform: 'capitalize' }}>
                            welcome to cookies cart page
                        </Typography>
                    </Container>


                    {
                        pr.length === 0 ? <Container>
                            <Grid container justifyContent='center' alignItems='center' style={{ textTransform: 'capitalize' }}>

                                <Grid item >
                                    <Typography variant='h6' style={{ marginBottom: "16px" }}>
                                        Cart is empty
                                    </Typography>
                                    <Button onClick={MyShoping} color='primary'>
                                        Continue Shopping
                                    </Button>
                                </Grid>

                            </Grid>


                        </Container> : <div>


                            <Container>

                                {
                                    pr.map((i) => {

                                        return (
                                            <Paper key={i.productId} style={{ textTransform: 'capitalize', paddingBottom: "16px", paddingTop: "8px", marginBottom: "20px" }}>

                                                <Grid container>

                                                    <Grid item xs container sm={2} justifyContent='center' alignItems='center'>

                                                        <Grid item >
                                                            <img src={i.productImage} alt={i.name} style={{ maxWidth: "100%", height: "200px" }}></img>
                                                        </Grid>
                                                    </Grid>

                                                    <Grid item sm xs>
                                                        <Typography variant='h6' className={classes.f1} >
                                                            {i.name}
                                                        </Typography>
                                                        <Typography variant='h6' className={classes.f1} >

                                                            amount <i className="fa fa-inr" aria-hidden="true"></i> {i.totalamount}
                                                        </Typography>
                                                        <Typography className={classes.f2} >
                                                            deliveryCharge <i className="fa fa-inr" aria-hidden="true"></i> {i.totaldelivery}
                                                        </Typography>
                                                        <Typography className={classes.f2} >

                                                            qutanity {i.quantity}
                                                        </Typography>

                                                        <SelecCom id={i.productId} updateState={updateStatebyUpdate}></SelecCom>
                                                        <MyBtnForD id={i.productId} deleteItemChangeState={deleteItemChangeState} ></MyBtnForD>

                                                    </Grid>

                                                </Grid>

                                            </Paper>
                                        )
                                    })
                                }
                            </Container>


                            <Container>

                                <Grid container style={{ textTransform: 'capitalize' }}>

                                    <Grid item >
                                    <Typography style={{ marginBottom: "8px" }}>
                                            total quantity     {
                                                pr.reduce((acc, i) => {

                                                    return i.quantity + acc;

                                                }, 0)
                                            }
                                        </Typography>
                                        
                                        <Typography style={{ marginBottom: "8px" }}>
                                            total amount  <i className="fa fa-inr" aria-hidden="true"></i>    {
                                                pr.reduce((acc, i) => {

                                                    return acc + i.totalamount

                                                }, 0)
                                            }
                                        </Typography>

                         

                                        <Typography style={{ marginBottom: "8px" }}>
                                            Total deliveryCharge <i className="fa fa-inr" aria-hidden="true"></i>   {
                                                pr.reduce((acc, i) => {

                                                    return acc + i.totaldelivery;

                                                }, 0)
                                            }
                                        </Typography>

                                        <Typography variant='h6' style={{ marginBottom: "8px" }}>
                                            total bill amount <i className="fa fa-inr" aria-hidden="true"></i>  {
                                                pr.reduce((acc, i) => {

                                                    return acc + i.totaldelivery + i.totalamount;

                                                }, 0)
                                            }
                                        </Typography>
                                        <Box margin={"16px 0px"}>
                                            <Button onClick={checkout} variant='contained' color='primary'>
                                                Checkout
                                            </Button>
                                        </Box>
                                    </Grid>

                                </Grid>

                            </Container>



                        </div>
                    }


                </>
            }



        </div>
    )
}


function MyBtnForD({ id, deleteItemChangeState }) {

    async function deletecart() {

       // console.log("delete cart button clicked");

        //console.log(id);
        // post request karo 


        try {
            const { data } = await deletecartCookie({ productId: id });

            //console.log(data);

            if (data.or) {
                return deleteItemChangeState(data.result);
            }
        } catch (error) {

            //console.log(error);

        }
    }

    return (<Button onClick={deletecart} variant='contained' color='primary'>
        Remove
    </Button>)



}



function SelecCom({ id, updateState }) {


    const [qty, setQty] = useState(1);

    async function update(d) {

        //console.log("update button clicked");
       // console.log(d);
        //console.log(id);


        try {
            const { data } = await updatecartCookie({ productId: id, quantity: parseInt(d) });

           // console.log(data);

            if (data.or) {
                return updateState(data.result);

            }
            //set state 
        } catch (error) {

           // console.log(error);

        }


    }
    return (
        <div>
            <Grid container spacing={3} alignItems='center' style={{ marginBottom: "16px" }}>
                <Grid item >
                    <Typography>
                        Select Quantity
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={4}>
                    <TextField select label='Select Quanity' value={qty} onChange={(e) => {
                        setQty(e.target.value);
                        update(e.target.value);

                    }} variant='outlined' size='small' style={{ width: "100%" }}>
                        <MenuItem value={1} > 1
                        </MenuItem>
                        <MenuItem value={2}> 2
                        </MenuItem>
                        <MenuItem value={3}> 3
                        </MenuItem>
                      

                    </TextField>
                </Grid>
            </Grid>

        </div>
    )
}



