import { createSlice } from '@reduxjs/toolkit';

const initialState={
    isAuth:false,
    customer:null,
    otp:{
        phone:'',
        hash:'',
    }

};

export const authSlice=createSlice({

    name:'auth',
    initialState,
    reducers:{
        setAuth: (state,action) => {
           // state={isAuth:"",seller:{}}

           const{customer}=action.payload;//{customer:{}}

           state.isAuth=true;
           
           state.customer=customer;

          },
          setOtp:(state,action)=>{
              const{hash,phone}=action.payload;// pura data {hash:"",phone:""}
              state.otp.hash=hash;
              state.otp.phone=phone;
          },
          setLogout:(state,action)=>{

            state.isAuth=false;
            state.customer=action.payload;

          },

    },
});

export const {setAuth,setOtp,setLogout}=authSlice.actions;

export default authSlice.reducer;



