import { Container, Grid, Typography } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import { getSubProdutsbysubcateroryid } from '../../http/index';
import MyLoader from '../../components/MyLoading/MyLoader';

import { Link } from 'react-router-dom';
export default function AllSubProducts() {

    const { id } = useParams();

    const [d, setD] = useState([]);

    const [ld, setld] = useState(true);

    useEffect(() => {
        (async () => {

            try {

                const { data } = await getSubProdutsbysubcateroryid(id);

                //console.log(data);

                if (data.or) {

                    setD(data.result);
                }
            } catch (error) {

                // console.log(error);

            }

            return setld(false);
        })()
    }, [id])
    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container style={{ margin: "32px 0px ", textTransform: 'capitalize', textAlign: 'center' }}>

                        <Typography variant='h6'>Welcome to Produts pages</Typography>
                    </Container>

                    <Container>

                        <Grid container spacing={3} style={{ textTransform: 'capitalize' }}>

                            {
                                d.map((i) => {

                                    return (<Grid key={i._id} xs={6} sm={3} item container justifyContent='center' alignItems='center'>

                                        <Grid item>

                                            <Link to={`/pr/${i._id}`} style={{ display: 'block', textDecoration: 'none', color: "black" }}>

                                                <Grid container justifyContent='center'>

                                                    <Grid item>
                                                        <Typography variant='subtitle2' style={{ marginBottom: "4px" }}>
                                                            {
                                                                parseInt(i.discount) === 0 ? <></> : <>
                                                                    <span style={{ color: "red" }}>
                                                                        {parseInt(i.discount)}% off
                                                                    </span>
                                                                </>
                                                            }
                                                        </Typography>
                                                        <img src={i.mainImage} alt={i.name} style={{ maxWidth: "100%", maxHeight: "220px" }}></img>
                                                    </Grid>

                      

                                                </Grid>

                                            
                                                    <Typography variant='subtitle2' style={{ marginTop: "8px", marginBottom: "4px" }}>
                                                        {i.name}
                                                    </Typography>

                                                    <Grid container  style={{marginTop:"4px",marginBottom:"4px"}}>

                                                    <Grid item style={{marginRight:"8px"}}>
                                                            <Typography variant='subtitle2' >
                                                                Price {" "} <span>
                                                                    <i className="fa fa-inr" aria-hidden="true"></i> {i.spInGst} {" "}
                                                                </span>
                                                            </Typography>

                                                        </Grid>
                                                        <Grid item>
                                                            <Typography variant='subtitle2'  >
                                                                MRP {" "} <span style={{textDecoration:'line-through'}}>
                                                                    <i className="fa fa-inr" aria-hidden="true"></i> {i.mrp}
                                                                </span>
                                                            </Typography>
                                                        </Grid>
                                                        
                                                    </Grid>
                                                    {
                                                        i.deliveryCharge === 0 ? <Typography >

                                                            FREE Delivery by StoreClubs
                                                        </Typography> : <Typography >
                                                            Delivery Charge {i.deliveryCharge}
                                                        </Typography>
                                                    }
                                            


                                            </Link>

                                        </Grid>
                                    </Grid>)
                                })
                            }

                        </Grid>
                    </Container>


                </>
            }

        </div>
    )
}
