import { Container, TextField, Typography, Box, Button, Grid } from '@material-ui/core';
import React, { useEffect, useState } from 'react';

import MyLoader from '../../components/MyLoading/MyLoader';

import { getCustomerDataEdit, addCustomerDataEdit } from '../../http/index';

import { useHistory } from 'react-router-dom';
export default function EditProfile() {

    const [ld, setld] = useState(true);
    let [name, setName] = useState('');

    let [address1, setAdd1] = useState('');

    let [address2, setAdd2] = useState('');

    const [d, setData] = useState({ fullAddress: {}, pincodeId: {} });

    const [s, setS] = useState(false);

    const history = useHistory();
    async function submit(e) {
        e.preventDefault();

        //console.log("button clicked");



        const condata = {
            name: name.toLowerCase(),
            address1: address1.toLowerCase(),
            address2: address2.toLowerCase(),
        };

        try {

            const { data } = await addCustomerDataEdit(condata);

            //console.log(data);

            if (data.or) {

                return setS(true);

            }
        } catch (error) {

            //console.log(error);

        }

    }

    useEffect(() => {

        //console.log("useeffect calling for customer data profile data edit");

        (async () => {

            try {

                const { data } = await getCustomerDataEdit();

               // console.log(data);

                const { result } = data;


                if (data.or) {

                    setData(result);
                    setName(result.name);
                    setAdd1(result.address1);
                    setAdd2(result.address2);

                }

            } catch (error) {

                //console.log(error);

            }

            return setld(false);
        })();


    }, [])

    return (
        <div>


            {
                ld ? <MyLoader></MyLoader> : <>

                    <Container>
                        <Typography variant='h6' style={{ textAlign: 'center', marginTop: "32px", marginBottom: "32px", textTransform: 'capitalize' }}>
                            welcome to edit profile page
                        </Typography>

                    </Container>

                    {
                        s ? <Container>

                            <Grid container justifyContent='center' alignItems='center' style={{ minHeight: "200px" }} >

                                <Grid item >
                                    <Typography style={{ textTransform: 'capitalize', color: 'green', marginBottom: "24px" }}>
                                        user profile sccuessfully updated
                                    </Typography>
                                    <Button variant='contained' color='primary' onClick={() => {
                                        history.push('/profile')
                                    }}>
                                        Go to Profile
                                    </Button>
                                </Grid>
                            </Grid>

                        </Container> : <Container>
                            <form>

                                <Grid container justifyContent='center' alignItems='center' style={{ textTransform: 'capitalize' }} >

                                    <Grid item xs={12} sm={6}>

                                        <Grid container spacing={2} alignItems='center' style={{ marginBottom: "32px" }}>

                                            <Grid item >
                                                <Typography>
                                                    name
                                                </Typography>
                                            </Grid>
                                            <Grid item xs>
                                                <TextField type="text" variant='outlined' size='small' style={{ width: "100%", textTransform: 'capitalize' }} value={name} onChange={(e) => {
                                                    setName(e.target.value);
                                                }}></TextField>
                                            </Grid>
                                        </Grid>

                                        <Box style={{ marginBottom: "32px" }}>
                                            <Typography>
                                                phone {d.phone}
                                            </Typography>
                                        </Box>

                                        <Grid container spacing={2} alignItems='center' style={{ marginBottom: "32px" }}>

                                            <Grid item >
                                                <Typography>
                                                    first address
                                                </Typography>
                                            </Grid>
                                            <Grid item xs>
                                                <TextField type="text" variant='outlined' size='small' style={{ width: "100%", textTransform: 'capitalize' }} value={address1} onChange={(e) => {
                                                    setAdd1(e.target.value);
                                                }}>

                                                </TextField>
                                            </Grid>
                                        </Grid>
                                        <Grid container spacing={2} alignItems='center' style={{ marginBottom: "32px" }}>

                                            <Grid item >
                                                <Typography>
                                                    second  address
                                                </Typography>
                                            </Grid>
                                            <Grid item xs>
                                                <TextField type="text" variant='outlined' size='small' style={{ width: "100%", textTransform: 'capitalize' }} value={address2} onChange={(e) => {
                                                    setAdd2(e.target.value);
                                                }} >

                                                </TextField>
                                            </Grid>
                                        </Grid>

                                        <Box style={{ marginBottom: "32px" }}>
                                            <Typography>
                                                full addressn   {
                                                    d.fullAddress.nameOfCity
                                                }
                                                {" "}
                                                {
                                                    d.fullAddress.stateOfCity
                                                }
                                                {" "}
                                                {
                                                    d.fullAddress.countryOfCity
                                                }
                                            </Typography>

                                        </Box>
                                        <Box style={{ marginBottom: "32px" }}>
                                            <Typography>
                                                pincode {d.pincodeId.pincode}
                                            </Typography>
                                        </Box>
                                        <Box>
                                            <Button onClick={submit} variant='contained' color='primary'>
                                                submit
                                            </Button>
                                        </Box>
                                    </Grid>


                                </Grid>

                            </form>
                        </Container>



                    }


                </>
            }


        </div>
    )
}
