import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import { setBuyNu } from '../../store/buyNowSlice';
import { addItemToCustomerCart, getuserProfile, getAddressForForm } from '../../http/index';
import StepOfAdress from '../../components/StepOfAdress/StepOfAdress';

import StepOfOrderSumaryById from '../../components/StepOfOrderSummaryById/StepOfOrderSummaryById';
import MyAddOrder from '../../components/MySteps/MyAddOreder';
import MyLoader from '../../components/MyLoading/MyLoader';

const Steps = {

    0: StepOfAdress,
    1: StepOfOrderSumaryById,

};

export default function SingleCheckout({ id }) {

    // console.log("single item checkout ");

    const disptach = useDispatch();

    const [ld, setld] = useState(true);

    const { pr, productId } = useSelector((state) => state.buy);// viewcheckout?pr=101

    const { customer } = useSelector((state) => state.auth);

    // post ke request karenhai then userprofile onnext
    // cstep ke onNext data 

    const [step, setStep] = useState(0);

    const Cstep = Steps[step];

    const [data, setData] = useState({ fullAddress: {}, pincodeId: {} });
    const [addressData, setAddressData] = useState({});


    useEffect(() => {

        //console.log("use effect calling single check out");

        (async () => {

            if (pr) {
                try {
                    const resultofcart = await addtocartinSingle(productId);

                    //console.log("item added succesffyly in cart from fronetnd");

                    //console.log(resultofcart);

                    // set buystate 
                    if (resultofcart.or) {
                        disptach(setBuyNu());//false
                    }


                } catch (error) {

                    //console.log(error);

                }
            } else {
                //console.log("pr buy now is false so no need to request add to cart");
            }

            //then call for user profile 

            if (customer.activated) {
                //console.log("customer is activated so you can call for user profile ");

                try {
                    const myuser = await getUserP();
                    // console.log(myuser);

                    if (myuser.or) {
                        setData(myuser.result);
                    }


                } catch (error) {

                    //console.log(error);
                }

            } else {

                //console.log("customer is not activated no request to sever");
                //console.log("request for address form details ");

                try {
                    const d = await getAddressForCustomer();

                    //console.log(d);

                    if (d.or) {
                        setAddressData(d.result);
                    }
                } catch (error) {

                    //console.log(error);

                }




            }

            return setld(false);


            function getAddressForCustomer() {

                return new Promise(async (resolve, reject) => {

                    try {
                        const { data } = await getAddressForForm();

                        return resolve(data);

                    } catch (error) {

                        return reject(error);

                    }
                });


            }



        })();


        function addtocartinSingle(p) {

            return new Promise(async (resolve, reject) => {

                try {
                    const { data } = await addItemToCustomerCart({ productId: p });

                    return resolve(data);
                } catch (error) {
                    return reject(error);

                }
            })
        }

        function getUserP() {

            return new Promise(async (resolve, reject) => {

                try {
                    const { data } = await getuserProfile();
                    return resolve(data);
                } catch (error) {

                    return reject(error);

                }
            })

        }

    }, [pr, productId, customer, disptach]);

    function onNext() {

        setStep(step + 1);

    }

    // steps 
    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <MyAddOrder st={step}></MyAddOrder>

                    <Cstep onNext={onNext} id={id} data={data} addressData={addressData}></Cstep>
                </>
            }

        </div>
    )
}
