import React, { useEffect, useState } from 'react';

import { useParams } from 'react-router-dom';

import { getSingleCustomerService } from '../../http/index';

import { Container, Grid, Typography } from '@material-ui/core';
import moment from 'moment';

import MyLoader from '../../components/MyLoading/MyLoader';

export default function SingleCustomerService() {

  const [ld, setld] = useState(true);

  const { id } = useParams();

  const [md, setmd] = useState({ fullAddress: {}, pincodeId: {} });


  useEffect(() => {

    (async () => {

      try {

        const { data } = await getSingleCustomerService(id);

        //console.log(data);

        if (data.or) {

          setmd(data.result);

        }
      } catch (error) {

        //console.log(error);

      }

      return setld(false);


    })()

  }, [id]);



  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>

          <Container style={{ margin: "32px 0px ", textTransform: 'capitalize', textAlign: 'center' }}>

            <Typography variant='h6'>
              Welcome to Single Booking page
            </Typography>
          </Container>

          <Container>
            <Grid container justifyContent='space-between' alignItems='center' style={{ textTransform: 'capitalize', marginBottom: "16px" }}>

              <Grid item>

                <Typography style={{ marginTop: "8px" }}>
                  Booking id {md._id}
                </Typography>
                <Typography style={{ marginTop: "8px" }}>
                  {md.workname}  {moment(md.createdAt).format('LLL')}
                </Typography>
                <Typography style={{ color: 'blue', marginTop: "8px" }}>
                  Booking Status {md.bookingStatus}
                </Typography>
              </Grid>

              <Grid item >
                <Typography style={{ marginBottom: "8px" }}>
                  address   {md.address}
                </Typography>
                <Typography>
                  {md.fullAddress.nameOfCity} {" "}  {md.fullAddress.stateOfCity} {" "}  {md.fullAddress.countryOfCity} {md.pincodeId.pincode}
                </Typography>
              </Grid>
            </Grid>
            <Typography variant='h6' style={{ textTransform: 'capitalize', marginBottom: "24px" }}>
              Your item list
            </Typography>
            <Grid container alignItems='center' justifyContent='center'>

              <Grid item >
                <img src={md.workimage} alt={md.workname} style={{maxWidth:"100%",height:"500px"}}></img>

              </Grid>
            </Grid>
          </Container>
        </>
      }



    </div>
  )
}
