import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { getAllCustomerService } from '../../http/index';

import { Container, Grid, Typography } from '@material-ui/core';

import moment from 'moment';

import MyLoader from '../../components/MyLoading/MyLoader';


export default function CustomerService() {

  const [ld, setld] = useState(true);

  const [md, setmd] = useState([{ fullAddress: {}, pincodeId: {} }]);

  useEffect(() => {

    (async () => {

      try {

        const { data } = await getAllCustomerService();

        // console.log(data);

        if (data.or) {

          setmd(data.result);

        }
      } catch (error) {

        //console.log(error);

      }

      return setld(false);
    })();


  }, []);

  return (
    <div>

      {
        ld ? <MyLoader></MyLoader> : <>

          <Container style={{ margin: "32px 0px ", textTransform: 'capitalize', textAlign: 'center' }}>

            <Typography variant='h6'>
              Welcome to Your Services Booking list
            </Typography>
          </Container>

          <Container>

            {
              md.map((i) => {

                return (<Grid container justifyContent='space-between' alignItems='center' key={i._id} style={{ textTransform: 'capitalize', marginBottom: "16px" }}>

                  <Grid item>
                    <Link to={`/myservices/${i._id}`} style={{ marginTop: "4px" }}>
                      {i._id}
                    </Link>
                    <Typography style={{ marginTop: "4px" }}>
                      {i.workname}  {moment(i.createdAt).format('LLL')}
                    </Typography>
                    <Typography style={{ color: 'blue', marginTop: "4px" }}>
                      Booking Status {i.bookingStatus}
                    </Typography>
                  </Grid>

                  <Grid item >
                    <Typography>
                      address   {i.address}
                    </Typography>
                    <Typography>
                      {i.fullAddress.nameOfCity} {" "}  {i.fullAddress.stateOfCity} {" "}  {i.fullAddress.countryOfCity} {i.pincodeId.pincode}
                    </Typography>
                  </Grid>
                </Grid>)
              })
            }
          </Container>


        </>
      }



    </div>
  )
}
