
import React from 'react';
import { useSelector } from 'react-redux';

import ActivateProfile from '../Activate/ActivateProfile';

import UserProfile from '../UserProfile/UserProfile';

export default function StepOfAdress({ onNext, data, addressData }) {

    //console.log("step of address");

    const { customer } = useSelector((state) => state.auth);

    return (
        <div>
          

            {
                customer.activated ? <UserProfile onNext={onNext} data={data} >

                </UserProfile> : <ActivateProfile onNext={onNext} addressData={addressData}></ActivateProfile>
            }

        </div>
    )
}
