import { Container, } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';

import StepOfAddress from '../../components/StepOfAdress/StepOfAdress';

import StepOfOrderSummary from '../../components/StepOfOrderSummary/StepOfOrderSummary';

import { getuserProfile, getAddressForForm } from '../../http/index';
import MyAddOrder from '../../components/MySteps/MyAddOreder';

import MyLoader from '../../components/MyLoading/MyLoader';



const Setps = {
    0: StepOfAddress,//data prop
    1: StepOfOrderSummary,
   
};


export default function AllCheckout() {


    const [ld, setld] = useState(true);


    const { customer } = useSelector((state) => state.auth);

    const [step, setStep] = useState(0);

    const CStep = Setps[step];

    function onNext() {

        setStep(step + 1);

    }

    const [data, setData] = useState({ fullAddress: {}, pincodeId: {} });

    const [addressData, setAddressData] = useState({ city: [], state: [], country: "", pincode: [] });


    // use effect ke profile miltajath thi

    useEffect(() => {

        //console.log("useeffect call in all checkout component");

        (async () => {


            //if customer.actevated
            if (customer.activated) {
                //console.log("customer is activated so you can call for user profile ");

                try {
                    const data = await getUserProfile();
                    //console.log(data);

                    if (data.or) {
                        setData(data.result);
                    }

                } catch (error) {
                    //console.log(error);

                }

            } else {

                //get address details for customer
               // console.log("user is not activated so we not call user prifile no request go");
                //console.log("request address details for application");

                try {
                    const d = await getAddressForCustomer();
                   // console.log(d);
                    if (d.or) {
                        setAddressData(d.result);
                    }
                } catch (error) {

                    //console.log(error);

                }

            }

            return setld(false);



        })();



        function getUserProfile() {

            return new Promise(async (resolve, reject) => {
                try {
                    const { data } = await getuserProfile();

                    return resolve(data);
                } catch (error) {
                    return reject(error);

                }
            });

        }

        function getAddressForCustomer() {

            return new Promise(async (resolve, reject) => {

                try {
                    const { data } = await getAddressForForm();

                    return resolve(data);

                } catch (error) {

                    return reject(error);

                }
            });


        }


    }, [customer]);

    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container>
                        <MyAddOrder st={step} ></MyAddOrder>

                    </Container>

                    <CStep onNext={onNext} data={data} addressData={addressData}></CStep>

                </>
            }

        </div>
    )
}
