import React, { useState } from 'react'

import SetpPhone from '../../components/SetpPhone/SetpPhone';

import SetpOtp from '../../components/SetpOtp/SetpOtp';
import { Container, Typography } from '@material-ui/core';

const Step = {
    1: SetpPhone,
    2: SetpOtp
};
export default function NormalLogin() {

    const [step, setStep] = useState(1);

    const Cstep = Step[step];

    function onNext() {
        setStep(step + 1);
    }

    return (
        <div>

            <Container>
                <Typography variant='h6' style={{ marginTop: "32px", marginBottom: "32px", textAlign: 'center', textTransform: 'capitalize' }}>
                    welcome to  LoginPage

                </Typography>
            </Container>

            <Cstep onNext={onNext}></Cstep>
        </div>
    )
}
