import React, { useState, useEffect } from 'react';

import { getallshopbycatergory } from '../../http/index';

import { Container, Grid, Typography } from '@material-ui/core'

import { Link } from 'react-router-dom';

import MyLoader from '../../components/MyLoading/MyLoader';

export default function AllCatergory() {

    const [ld, setld] = useState(true);

    const [d, setD] = useState([]);

    useEffect(() => {

        (async () => {

            try {

                const { data } = await getallshopbycatergory();

                //console.log(data);

                if (data.or) {

                    setD(data.result);

                }

            } catch (error) {

                //console.log(error);
            }

            return setld(false)

        })();
    }, [])


    return (
        <div>

            {
                ld ? <MyLoader></MyLoader> : <>
                    <Container style={{ margin: "32px 0px", textTransform: 'capitalize', textAlign: 'center' }}>
                        <Typography variant='h6' >
                            welcome to All Catergory page
                        </Typography>

                    </Container>

                    <Container>
                        <Grid container style={{ marginTop: "24px", textTransform: 'capitalize', marginBottom: "24px" }} spacing={2}>

                            {
                                d.map((i) => {
                                    return (<Grid key={i._id} item xs={6} sm={3} container justifyContent='center' alignItems='center'>

                                        <Grid item >
                                            <Link to={`/catergory/${i._id}`} style={{ textDecoration: 'none', color: 'black', display: 'block' }}>



                                                <Grid container justifyContent='center'>

                                                    <Grid item>
                                                        <img src={i.catergoryimage} alt={i.name} style={{ maxWidth: "100%", maxHeight:"120px" }}>
                                                        </img>
                                                    </Grid>
                                                </Grid>

                                                <Typography variant='subtitle2' style={{ marginTop: "4px", textAlign: 'center' }}  >{i.name}</Typography>
                                            </Link>
                                        </Grid>


                                    </Grid>)
                                })
                            }
                        </Grid>
                    </Container>
                </>
            }



        </div>
    )
}
