import React, { useState } from 'react';

import { registerCustomerWork } from '../../http/index';

import { useHistory } from 'react-router-dom';

import { Button, Container, Grid, TextField, Typography } from "@material-ui/core";

export default function BookService() {

    const [name, setName] = useState('');

    const [fa, setfa] = useState(false);

    const [workimage, setworkimage] = useState('');

    const [er, setEr] = useState(false);

    const history = useHistory();

    function st() {

        history.push('/myservices');

    }
    async function submit(e) {

        e.preventDefault();

        //console.log("submiited button clicked");
        //workname

        if (!workimage || !name) {

            setEr(true);

            return;
        }

        const mydata = new FormData();

        mydata.append("workname", name.toLowerCase());

        mydata.append("workimage", workimage);

        try {

            const { data } = await registerCustomerWork(mydata);

            // console.log(data);

            if (data.or) {
                setfa(true);

            }

        } catch (error) {

            //console.log(error);

        }

    }


    return (
        <div>

            <Container style={{ margin: "32px 0px", textTransform: 'capitalize', textAlign: 'center' }}>
                <Typography variant='h6' >
                    Instant Order By Upload List
                </Typography>
            </Container>
            <Container>

                <Grid container justifyContent='center' alignItems='center' style={{ marginTop: "16px" }}>

                    <Grid item >
                        {
                            fa ? <div>
                                <Typography style={{ textTransform: 'capitalize', color: 'green', marginTop: "16px", marginBottom: "16px" }}>
                                    Order-Service is booking successfully done
                                </Typography>
                                <Button color='primary' variant='contained' onClick={st}>
                                 go to Order-Services
                                </Button>
                            </div> : <>
                                <form>

                                    {
                                        er && <Grid container alignItems='center' justifyContent='center' spacing={2} style={{ textTransform: 'capitalize', marginBottom: "16px" }}>
                                            <Grid item >
                                                <Typography style={{ color: "red" }}>
                                                    please fill all feild of form
                                                </Typography>
                                            </Grid>
                                        </Grid>
                                    }

                                    <Grid container alignItems='center' spacing={2} style={{ textTransform: 'capitalize' }}>
                                        <Grid item >
                                            <Typography>
                                                Enter name of catergory item
                                            </Typography>
                                        </Grid>

                                        <Grid item sm xs={12}>
                                            <TextField style={{width:"100%"}} size='small' fullWidth placeholder='kiarna or vegetbale or fruits' variant='outlined' value={name} onChange={(e) => {
                                                setName(e.target.value);
                                            }}>

                                            </TextField>
                                        </Grid>
                                    </Grid>

                                    <Grid container alignItems='center' spacing={2} style={{ textTransform: 'capitalize', marginTop: "16px" }}>
                                        <Grid item >
                                            <Typography>
                                                Upload Your Item list
                                            </Typography>
                                        </Grid>

                                        <Grid item sm xs={12}>
                                            <TextField style={{width:"100%"}} size='small' type={'file'} variant='outlined' onChange={(e) => {
                                                setworkimage(e.target.files[0]);

                                            }}>

                                            </TextField>
                                        </Grid>
                                    </Grid>

                                    <Button onClick={submit} style={{ marginTop: "24px" }} color='primary' variant='contained'>
                                        Order  Now
                                    </Button>

                                </form>


                            </>
                        }


                    </Grid>
                </Grid>
            </Container>



        </div>
    )
}
